<script>
  import {
    initShare
  } from "@/utils/wechatShare.js";
  import headerTwo from "../../components/header2";
  import headerMobile from "../../components/headerMobile";
  import footerTwo from "../../components/footer2";
  import "../../common/js/activityExecution.js";
  import Swiper from "swiper";
  import "swiper/dist/css/swiper.min.css";
  import {
    Loading
  } from "element-ui";


  export default {
    data() {
      return {
        serviceItems: [],
        activeCaseList: [],
        isPc: true,
        viewNum: 4,
        imgIndex: 1,
        flag: true,
        isDetail: false
      };
    },
    //判断路由跳转到哪儿时执行
    beforeRouteUpdate(to, from, next) {
      if (to.name == 'activityExecution') {
        this.isDetail = false
      }
      next();
    },
    mounted() {
      //判断当前路由是否是详情
      if(this.$route.path=='/activityExecution/activityDetails'){
        this.isDetail = true
      }

      Loading.service();

      this.setWidth();
      this.leftImg();
      this.animate();
      this.getServiceItems().finally(() => {
        this.getActiveCaseList().finally(() => {
          this.animated();
          Loading.service().close();

        });
      });

      const shareData = {
        title: '精准把控，高端、严谨、负责地完成活动',
        desc: '中旌影视传媒',
        link: location.href,
        imgUrl: 'https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/activity111.png',
        signUrl: encodeURIComponent(location.href)
      }
      initShare(shareData);
    },
    methods: {
      mouseEnterText(event) {
        let getimgXmText = event.currentTarget;
        $(getimgXmText).find(".shadeText").fadeIn();
      },
      mouseLeaveText(b) {
        let getimgXmText = event.currentTarget;
        $(getimgXmText).find(".shadeText").fadeOut();
      },
      setWidth() {
        var widNum = $(window).width();
        if (widNum > 990) {
          this.clientType = "";
          this.isPc = true;
        } else {
          this.clientType = "MOBILE";
          this.isPc = false;
        }
      },
      //获取服务项目数据方法
      getServiceItems() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "activity/execution/getServiceItems",
            method: "get",
            params: params
          })
          .then(res => {
            this.serviceItems = res.results.details;
            // console.log(res);        
          })
          .catch(error => {
            console.log(error);
          });
      },
      //获取案列滚动数据方法
      getActiveCaseList() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "activity/execution/getActiveCaseList",
            method: "get",
            params: params
          })
          .then(res => {
            // console.log(res.results);
            this.activeCaseList = res.results;
            this.$nextTick(function () {
              this.setNum();
            });
          })
          .catch(error => {
            console.log(error);
          });
      },
      setNum() {
        var widNum = $(window).width();
        var viewNum;
        const that = this;
        if (widNum > 990) {
          this.viewNum = 4;
        } else {
          this.viewNum = 2;
        }
        var swiper = new Swiper(".swiper-container", {
          slidesPerView: this.viewNum,
          spaceBetween: 30,
          autoplay: true,
          observer: true,
          observeParents: true,
          loop: false,
          speed: 500,
          navigation: {
            nextEl: ".rightBtn",
            prevEl: ".leftBtn"
          },
          autoplay: {
            disableOnInteraction: false // 手动滑动后继续自动播放
          },
          on: {
            slideChangeTransitionStart: function () {
              that.imgIndex = this.realIndex + 1;
            }
          },
          autoplayDisableOnInteraction: false
        });
      },
      setInfoShow(event) {
        let getMenuText = event.currentTarget;
        $(getMenuText).find(".textBox").fadeIn();
      },
      setInfoHide(event) {
        let getMenuText = event.currentTarget;
        $(getMenuText).find(".textBox").fadeOut();
      },
      animated() {
        var serveFwBox = document.getElementById("serveFwBox");
        serveFwBox.classList.add("animated");
      },
      routerTo(id,index) {
        this.isDetail = true
        this.$router.push({
          name: 'activityDetails',
          params: {
            caseId: id,
            caseIndex:index
          }
        });
      },
      leftImg() {
        if (document.body.offsetWidth <= 800) {
          this.flag = true;
        } else {
          this.flag = false;
        }
      },
      //动画特效
        animate() {
          var a, b, c;
          a = $(window).height();
          $(window).scroll(function() {
            var b = $(this).scrollTop();
            $(".main .liInfo").each(function() {
              c = $(this).offset().top;
              if(a + b > c) {
                $(this).find(".serveXm-left img").addClass("title-animate");
                setTimeout(() => {
                  $(this).find(".serveTitle,.serveText").addClass("title-animate");
                }, 500)
              }
            });
          });
        }
    },
    components: {
      headerTwo,
      footerTwo,
      headerMobile
    }
  };

</script>

