      transform: rotate(-152deg);
    }

    .line_data p:nth-child(4) {
      width: 55px;
      top: 270px;
      left: 85px;
      transform: rotate(152deg);
    }

    .topTitle {
      padding: 30px 0;
    }
    .serveXm{
        padding: 0px 0 30px 0;
    }
    .line_obj {
      margin: 15px auto 0 auto;
      max-width: 220px;
    }

    .serveFw {
      height: 600px;
    }

    .topTitle img {
      width: 28px;
      margin-bottom: 0;
    }

    .topTitle p {
      font-size: 22px;
      margin-top: 6px;
    }

    .d_c img {
      padding-bottom: 2px;
    }

    .d_c span {
      padding-bottom: 0px;
    }

    .line_data {
      max-width: 220px;
    }

    @keyframes move {
      0% {
        width: 100%;
      }

      100% {
        width: 100%;
      }
    }

    .serveTitle {
      font-size: 20px;
      margin-top: 40px;
    }

    .serveText {
      font-size: 16px;
    }
  }

</style>



