<template>
  <baidu-map class="map" :center="{lng: 108.9605738677, lat: 34.1978921421}" :zoom="18">
    <bm-marker :position="{lng: 108.9605738677, lat: 34.1978921421}" :dragging="true" animation="BMAP_ANIMATION_BOUNCE">
    </bm-marker>
  </baidu-map>
</template>

<script>
export default {
  data () {
    return {
      show: false
    }
  },
  methods: {
    infoWindowClose () {
      this.show = false
    },
    infoWindowOpen () {
      this.show = true
    }
  }
}
</script>
<style scoped>
.map{
  width: 100%;
  height: 100%;
}
</style>



