<template>
  <div id="activityDetails">
    <!-- 头部 -->
    <!-- <header-mobile v-show="!isPc"></header-mobile>
    <header-two v-show="isPc"></header-two> -->

    <div class="main">
      <div class="topContent">
        <div class="lips">
          <span @click="goCaseList()">案例</span> > {{infoList.title}}
        </div>
        <div class="contentBox">
          <div class="contentTitle">
            {{infoList.title}}
          </div>
          <div class="infoDetails">
          {{infoList.content}}
          </div>
        </div>
      </div>

      <!-- banner -->
      <div class="bannerImg" v-if="!isPc">
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(item,index) in bannerList" :key="index">
            <img :src="item.imageUrl">
          </van-swipe-item>
        </van-swipe>
      </div>

      <div class="elementBanner row" v-if="isPc">

        <div class="leftBtn" :style="btnHeight" @click="leftChangeImg()">
          <img src="../../img/arrow_ico_big_left.png">
        </div>
        <div class="bannerBox">
          <el-carousel trigger="click" arrow='never' :height="height" ref="banner" indicator-position='outside' @change="changeImg">
            <el-carousel-item v-for="(item,index) in bannerList" :key="index">
              <img class="bannerImgDetails" :src="item.imageUrl">
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="rightBtn" :style="btnHeight" @click="rightChangeImg()">
          <div class="imgNum"><span>{{activeIndex}}</span>/<span>{{bannerList.length}}</span></div>
          <img src="../../img/arrow_ico_big_right.png">
        </div>
      </div>
      <!-- banner结束 -->
      <div class="pageBox" >
        <div @click="goPrepCase()" class="pageBox-prev">
          <div v-show="this.nowCaseIndex>0">
            上一篇：{{this.caseIdList[this.nowCaseIndex-1]}}
          </div>         
          </div>
        <div @click="goNextCase()" class="pageBox-next">
          <div v-show="this.nowCaseIndex<this.caseIdList.length-1">
            下一篇:{{this.caseIdList[Number(this.nowCaseIndex)+1]}}
          </div>
          </div>
      </div>
    </div>
    <!-- 底部 -->
    <!-- <footer-two></footer-two> -->
  </div>
</template>

<script>
  import {
    initShare
  } from '@/utils/wechatShare.js';
  import headerTwo from "../../components/header2";
  import headerMobile from "../../components/headerMobile";
  import footerTwo from "../../components/footer2";
  import { Loading } from "element-ui";


  export default {
    data() {
      return {
        bannerList: [],
        activeCaseList:[],
        infoList: [],
        isPc: true,
        nowCaseIndex:0,
        caseIdList:[],
        height: 0,
        btnHeight: {
          height: '',
        },
        activeIndex: 1
      }
    },
    beforeMount() {
      //   动态获取element-ui 的banner组件高度  
      this.height = $(window).width() * 643 / 955 * 0.523 + 'px';
    },
    mounted() {
      // console.log(this.$route.params.caseIndex)
      if(this.$route.params.caseIndex){
        this.nowCaseIndex=this.$route.params.caseIndex
      }else{
        this.nowCaseIndex=sessionStorage.getItem('caseIndex')
      }
      // console.log(this.nowCaseIndex)
    Loading.service();
      if (this.$route.params.caseId) {
        sessionStorage.setItem('caseId', this.$route.params.caseId)
      }
      this.setWidth();
      this.getBanner().finally(()=>{
        this.getActiveCaseList()
        Loading.service().close();
      });
      // 动态绑定banner按钮高度
      this.btnHeight.height = this.height;
      // 微信分享
      const shareData = {
        title: '精准把控，高端、严谨、负责地完成活动',
        desc: '中旌影视传媒',
        link: location.href,
        imgUrl: 'https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/activity111.png',
        signUrl: encodeURIComponent(location.href)
      }
      initShare(shareData)

      
    },
    
    methods: {
      setWidth() {
        var widNum = $(window).width();
        if (widNum > 990) {
          this.clientType = '';
          this.isPc = true;
        } else {
          this.clientType = 'MOBILE';
          this.isPc = false;
        }
      },
      getBanner() {
        var self = this;
        var params = new URLSearchParams();
        params.append("caseId", sessionStorage.getItem('caseId'));
        return self
          .$request({
            url: "activity/execution/getActiveCaseByCaseId",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response);
            this.infoList = response.results
            this.bannerList = this.infoList.images;
          })
          .catch(error => {
            console.log(error);
          });
      },
      leftChangeImg() {
        this.$refs.banner.prev();
      },
      rightChangeImg() {
        this.$refs.banner.next();
      },
      changeImg(a) {
        this.activeIndex = a + 1
        // console.log(this.activeIndex)

      },
      goCaseList() {
        this.$router.push({
          name: 'activityExecution'
        })
      },
      //获取案列滚动数据方法
      getActiveCaseList() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "activity/execution/getActiveCaseList",
            method: "get",
            params: params
          })
          .then(res => {
            // console.log(res.results);
            this.activeCaseList = res.results;
            for(let i=0;i<this.activeCaseList.length;i++){
              this.caseIdList.push(this.activeCaseList[i].title)           
            }
          })
          .catch(error => {
            console.log(error);
          });
      },
      goNextCase(){
        if(this.nowCaseIndex<this.activeCaseList.length){
        this.nowCaseIndex++
        }
        sessionStorage.setItem('caseIndex',this.nowCaseIndex)
        sessionStorage.setItem('caseId', this.activeCaseList[this.nowCaseIndex].caseId)
        this.getBanner()       
      },
      goPrepCase(){
        if(this.nowCaseIndex>0){
        this.nowCaseIndex--
        }   
        sessionStorage.setItem('caseIndex',this.nowCaseIndex)
        sessionStorage.setItem('caseId', this.activeCaseList[this.nowCaseIndex].caseId)
        this.getBanner()       
      }
      
    },
    components: {
      headerTwo,
      headerMobile,
      footerTwo
    }

  }

</script>

<style scoped>
  /* banner开始 */
  .bannerImg img {
    width: 100%;
  }
  .pageBox{
    width: 70%;
    display: flex;
    height: 50px;
    line-height: 50px;
    margin: 0 15% 50px 15%;
  }
  .pageBox-prev{
    width: 50%;
    float: left;
    cursor: pointer;
  }
  .pageBox-next{
    width: 50%;
    float: right;
    text-align: right;
    cursor: pointer;
  }
  .imgNum {
    position: absolute;
    color: #707070;
    top: 55%;
    left: 50%;
    margin-left: -12px;
  }
  .infoDetails{
    text-indent: 2em;
    font-size: 18px;
    line-height: 34px;
  }
  .imgNum span {
    margin: 0 4px;
  }

  .elementBanner img {
    width: 100%;
  }
  .lips span{
    cursor: pointer;
  }

  .bannerBox >>>.el-carousel__button {
    width: 8px !important;
    border-radius: 50% !important;
    height: 8px !important;
    background-color: #000;
    border: solid 1px #000;
    opacity: 1;
  }

.bannerBox /deep/.el-carousel__indicators--horizontal {
    bottom: 25px;
  }
.bannerBox /deep/.el-carousel__indicators--outside{
  margin-top: 10px;
}

  .bannerBox /deep/ .el-carousel__indicator.is-active button {
    background-color: transparent !important;
    border: solid 1px #000;
    color: #fff;
  }

  element.style {
    display: none;
  }

  .el-carousel__arrow--left {
    left: 16px;
  }

  /* banner结束 */
  .topContent {
    margin-top: 144px;
  }

  .main {
    padding: 0 12%;
  }

  .contentBox {
    margin: 70px 0;
    font-size: 22px;
    line-height: 55px;
    color: #545454;
  }

  .contentTitle {
    font-size: 24px;
    line-height: 40px;
    color: #1D1D1D;
    text-align: center;
    margin-bottom: 36px;
  }

  .lips {
    color: #1D1D1D;
    font-size: 16px;
  }

  .lips span {
    color: #7D7D7D;
  }

  .el-carousel__arrow {
    display: none !important;
  }

  .elementBanner {
    position: relative;
    margin-bottom: 50px;
  }

  .leftBtn {
    width: 15%;
    height: 61%;
    background: #F7F7F7;
    position: absolute;
    z-index: 99;
    text-align: center;
    cursor: pointer;
  }

  .rightBtn {
    width: 15%;
    height: 61%;
    background: #F7F7F7;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 99;
    text-align: center;
    cursor: pointer;
  }

  .leftBtn img,
  .rightBtn img {
    width: 20px;
    position: absolute;
    top: 50%;
    margin-top: -8px;
  }

  .bannerBox {
    width: 70%;
    margin-left: 15%;
  }

  .el-carousel__indicators {
    position: relative;
    text-align: center;
    bottom: 0;
    margin-top: 10px;
  }

  @media screen and (max-width: 1200px) {
    .topContent {
      margin-top: 98px;
    }
  }

  @media screen and (max-width: 1010px) {
    .main {
      /* padding: 0 5%; */
      margin-bottom: 30px;
    }
  }

  @media screen and (max-width: 992px) {
    .pageBox{
      width: 100%;
      display: block;
      height: inherit;
      line-height: 40px;
      font-size: 14px;
      margin: 30px 0 0 0;
    }
    .pageBox-prev,.pageBox-next{
      width: 100%;
      float: none;
      text-align: left;
    }
    .infoDetails{
      font-size: 16px;
    }
    .main {
      padding: 0 5%;
      margin-bottom: 20px;
    }

    .topContent {
      margin-top: 70px;
    }

    .lips {
      display: none;
    }

    .contentBox {
      margin: 30px 0 !important;
      font-size: 18px !important;
      line-height: 36px !important;
      color: #545454;
    }

    .contentTitle {
      font-size: 20px !important;
      line-height: 30px !important;
      color: #1D1D1D;
      text-align: center;
      margin-bottom: 20px;
    }
  }

</style>



