<template>
  <div id="index1">
    <div id="wrap">
      <!-- 头部 -->
      <header-mobile v-show="!isPc"></header-mobile>
      <header-one v-show="isPc"></header-one>
      <!-- 内容区域 -->
      <div class="main">
        <!--联系电话-->
        <div class="phoneImg">
          <img src="../../img/homepage_right_tele.png">
        </div>
        <div class="page-wrapper">
          <ul>
            <!--首页banner-->
            <li>
              <div class="bannerImg">
                <van-swipe :autoplay="3000">
                  <van-swipe-item v-for="(image,index) in bannerList" :key="index" @click="bannerGo(image.linkType,image.linkUrl,image.linkModule)" >
                    <img :src="image.imageUrl">
                  </van-swipe-item>
                </van-swipe>
              </div>
            </li>
            <!-- 我们的业务1 -->
            <li>
              <business-one :ourBusinessList="ourBusinessList"></business-one>
            </li>
            <!-- 我们的业务2 -->
            <!-- <li>
              <business-two :ourBusinessList="ourBusinessList"></business-two>
            </li>-->
            <!-- 我们的资源 -->
            <li>
              <resource :ourResourcesList="ourResourcesList"></resource>
            </li>
            <!-- 联系我们 -->
            <li>
              <relation></relation>
            </li>
          </ul>
        </div>
      </div>
      <!-- 底部 -->
      <footer-one></footer-one>
    </div>
  </div>
</template>

<script>
import "../../common/js/index.js";
import { initShare } from '@/utils/wechatShare.js'
import { Loading } from 'element-ui';

import footerOne from "../../components/footer1";
import headerOne from "../../components/header1";
import headerMobile from "../../components/headerMobile";
import businessOne from "./children/businessOne";
// import businessTwo from "./children/businessTwo";

import resource from "./children/resource";
import relation from "./children/relation";

export default {
  data() {
    return {
      bannerList: [],
      ourBusinessList: [],
      ourResourcesList: [],
      clientType:'',
      isPc:true
    };
  },
  mounted() {
    Loading.service();
    this.setWidth();
    this.indexBanner().finally(()=>{
      this.ourBusiness().finally(()=>{
        this.ourResources();
        Loading.service().close();
      });
    });
    
    const shareData = {
      title: '西北首家数字化影传商业平台',
      desc: '中旌影视传媒',
      link: location.href,
      imgUrl:'https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/index111.png',
      signUrl: encodeURIComponent(location.href)
    }
    initShare(shareData)
  },
  updated() {
    this.$nextTick(function() {
      //我们的资源轮播
      $(".ck-slide").ckSlide({
        autoPlay: true
      });
      if(document.body.offsetWidth <= 800) {
        $(".businessTop-left img").css({'transform':'none'},{'transition':'none'});
      }
    });
    
  },

  methods: {
    setWidth(){
      var widNum = $(window).width();
      if (widNum > 990) {
        this.clientType = '';
        this.isPc=true
      } else {
        this.clientType = 'MOBILE';
        this.isPc=false
      }
    },
    indexBanner() {
      var self = this;
      var params = new URLSearchParams();
      params.append("clientType", this.clientType);
      return self
        .$request({
          url: "index/getBannerList",
          method: "get",
          params: params
        })
        .then(res => {
          this.bannerList = res.results;
          //  console.log(res);
        })
        .catch(error => {
          console.log(error);
        });
    },
    ourBusiness() {
      var self = this;
      var params = new URLSearchParams();
      return self
        .$request({
          url: "index/getOurBusinessList",
          method: "get",
          params: params
        })
        .then(res => {
          if (document.body.offsetWidth <= 800) {
            this.ourBusinessList = res.results;
          } else {
            let ourBusinessListTemp = [];
            let ourBusinessSubList = [];
            for (let index = 0; index < res.results.length; index++) {
              if (index * 2 < res.results.length) {
                const element1 = res.results[index * 2];
                ourBusinessSubList.push(element1);
              }
              if (index * 2 + 1 < res.results.length) {
                const element2 = res.results[index * 2 + 1];
                ourBusinessSubList.push(element2);
              }
              if (ourBusinessSubList.length > 0) {
                ourBusinessListTemp.push(ourBusinessSubList);
                ourBusinessSubList = [];
              }
            }
            // console.log(ourBusinessListTemp);
            this.ourBusinessList = ourBusinessListTemp;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    ourResources() {
      var self = this;
      var params = new URLSearchParams();
      return self
        .$request({
          url: "index/getOurResourcesList",
          method: "get",
          params: params
        })
        .then(res => {
          this.ourResourcesList = res.results;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // banner点击跳转
    bannerGo(linkType,linkUrl,linkModule){
      if(linkType){
        if(linkUrl){
          console.log(linkUrl)
          window.open(linkUrl, '_blank');
        }else if(linkModule){    
          this.$router.push(this.tf(linkModule.name))
        }
      }     
    },
    // 转换路由name
    tf(e){
      if(e=='INDEX'){
        return 'index'
      }else if(e=='FILMMAKING'){
        return 'videoMaking'
      }else if(e=='CREATIVE_PLATFORM'){
        return 'cultureCreativity'
      }else if(e=='INDUSTRY_RESOURCES'){
        return 'industryLeader'
      }else if(e=='PRODUCTS_CASE'){
        return 'productCase'
      }else if(e=='COOPERATE_RESOURCES'){
        return 'cooperation'
      }else if(e=='MARKETING_PLANNING'){
        return 'marketingPlan'
      }else if(e=='ACTIVITY_EXECUTION'){
        return 'activityExecution'
      }else if(e=='ABOUT_US'){
        return 'aboutUs'
      }
    },
  },
  components: {
    footerOne,
    headerOne,
    headerMobile,
    businessOne,
    // businessTwo,
    resource,
    relation
  }
};
</script>

<style scoped>
@import "../../common/css/anim.css";
@import "../../common/css/style.css";
@import "../../common/css/index.css";
.bannerImg img {
  width: 100%;
}
.van-swipe /deep/ .van-swipe__indicators{
      bottom: 50px;
}
.van-swipe /deep/ .van-swipe__indicator{
  opacity: 1;
  width: 12px;
  height: 12px;
  background: #fff;
  margin-right: 18px;
}
.van-swipe /deep/ .van-swipe__indicator:last-child{
  margin-right: 0;
}
.van-swipe /deep/ .van-swipe__indicator--active{
  background:transparent;
  border: 1px solid #fff;
}
@media screen and (max-width: 992px) {
  .van-swipe /deep/ .van-swipe__indicators{
      bottom: 16px;
}
.van-swipe /deep/ .van-swipe__indicator{
  opacity: 1;
  width: 8px;
  height: 8px;
  background: #fff;
  margin-right: 10px;
}
.van-swipe /deep/ .van-swipe__indicator--active{
  background:transparent;
  border: 1px solid #fff;
}
.van-swipe /deep/ .van-swipe__indicator:last-child{
  margin-right: 0;
}
}
</style>



