<template>
  <div id="footer3">
    <div class="footer">
      <div class="nav">
        <div class="col-md-7 footerList">
          <div class="footer-left">
            <div class="sitemap">
              <ul>
                <li v-for="(item,index) in headerList" :key="index">
                  <router-link :to="item.rouPath">
                    <a class="nav_link">{{item.headerName}}</a>
                  </router-link>
                </li>
              </ul>
            </div>
            <p class="record">2015-2019 西安中旌影视传媒股份有限公司 陕ICP备16010754号-1</p>
          </div>
        </div>
        <div class="col-md-1"></div>
        <div class="col-md-4">
          <div class="footer-right">
            <p class="contactUs">
              联系我们：
              <span>029-89555118</span>
            </p>
            <div class="contact-code">
              <ul>
                <li>
                  <img src="../img/footer_xiaochengxu.png">
                  <p>小程序</p>
                </li>
                <li>
                  <img src="../img/footer_weixinfuwu.png">
                  <p>微信服务</p>
                </li>
                <li>
                  <img src="../img/footer_weixindingyue.png">
                  <p>微信订阅</p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      headerList: [
        { rouPath: "/index", headerName: "首页" },
        { rouPath: "/cultureCreativity", headerName: "文化创意平台" },
        { rouPath: "/videoMaking", headerName: "影视制片制作" },
        { rouPath: "/marketingPlan", headerName: "营销策划" },
        { rouPath: "/activityExecution", headerName: "活动执行" },
        { rouPath: "/industryLeader", headerName: "行业领军资源" },
        { rouPath: "/productCase", headerName: "产品开发案例" },
        { rouPath: "/cooperation", headerName: "战略合作资源" },
        { rouPath: "/aboutUs", headerName: "关于我们" }
      ]
    };
  },
  mounted() {
    this.$router.afterEach((to, from, next) => {
      window.scrollTo(0, 0);
    });
  }
};
</script>

<style scoped>
/*footer*/
ul {
  padding: 0;
  margin: 0;
}
.footer-left {
  border-right: 1px solid #ececec;
}
.sitemap a {
  color: #7d8089 !important;
}
.footer {
  padding: 100px 100px 60px 240px;
}

.sitemap li {
  display: inline-block;
  padding: 0 15px;
  text-align: center;
  border-right: 1px solid #7d8089 !important;
  height: 16px;
  line-height: 16px;
  margin: 10px 0;
}

.sitemap li:last-child {
  border-right: none !important;
}

.nav_link {
  color: #7d8089 !important;
  font-size: 16px;
}

.record {
  margin-top: 100px;
  color: #636771;
  font-size: 14px;
  padding-left: 15px;
}

.contactUs {
  font-size: 16px;
  color: #1b1b1b;
  padding-left: 15px;
}

.contactUs span {
  font-size: 19px;
}

.contact-code img {
  width: 100%;
  display: block;
}

.contact-code li {
  display: inline-block;
  text-align: center;
  margin: 0 5px;
  padding: 0;
  width: 24%;
}

.contact-code {
  margin-top: 30px;
}

.contact-code p {
  color: #7d8089;
  font-size: 14px;
  padding-top: 10px;
}
.footerList{
  padding: 0;
}
@media screen and (max-width: 1570px) {
  .footer {
    padding: 100px 100px 60px 180px;
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    padding: 100px 100px 60px 120px;
  }
}

@media screen and (max-width: 1272px) {
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 50px 10px 30px 10px;
  }
  .record {
    margin-top: 50px;
  }
  .footer-left {
    border-right: none !important;
  }
  .contact-code ul{
  text-align: center;
}
.contactUs {    
    padding-left: 0;
}
}
/*footer end*/
</style>


