$(function() {
	//页面刷新回到顶部刷新数据
	$(window).scrollTop(0);
	//动画特效
	animate();
	
})



//动画特效
function animate() {
	var a, b, c;
	a = $(window).height();
	$(window).scroll(function() {
		var b = $(this).scrollTop();
		$(".infoBox .photoList").each(function() {
			c = $(this).offset().top;
			if(a + b > c) {
				setTimeout(() => {
					$(this).find(".titleText,.titleEn,.iconBox p").addClass("title-animate");
				}, 1000)
				setTimeout(() => {
					$(this).find(".imgBox img").addClass("title-animate");
				}, 3000)
			}
		});
	});
}


