import axios from 'axios'

const TokenKey = 'AccessTokenInfo'
const webappGateway = process.env.BASE_GAT + 'api/gateway/auth/'

export function getAccessToken() {
  return new Promise((resolve, reject) => {
    const AccessTokenInfo = JSON.parse(localStorage.getItem(TokenKey))
    if (AccessTokenInfo) {
      resolve(AccessTokenInfo)
    } else {
      var params = new URLSearchParams()
      params.append('appId', 'QkY5REU5MEYzMkNGMTM3Qg==')
      axios.post(webappGateway + 'getAccessToken', params)
        .then(function(response) {
          const data = response.data
          if (!data.hasError && data.code === 200) {
            localStorage.setItem(TokenKey, JSON.stringify(data.results))
            // console.log('获取AccessToken成功并写入缓存' + data.results.access_token)
            resolve(data.results)
          } else {
            // console.log(data.Message)
          }
        })
        .catch(function(error) {
          // console.log(error)
          reject(error)
        })
    }
  })
}

export function refreshAccessToken(refreshToken) {
  return new Promise((resolve, reject) => {
    var params = new URLSearchParams()
    params.append('refreshToken', refreshToken)
    axios.post(webappGateway + 'refreshToken', params)
      .then(function(response) {
        const data = response.data
        if (!data.hasError && data.code === 200) {
          localStorage.setItem(TokenKey, JSON.stringify(data.results))
          // console.log('刷新AccessToken成功并写入缓存' + data.results.access_token)
          resolve(data.results)
        } else {
          // console.log(data.Message)
          reject(data.Message)
        }
      })
      .catch(function(error) {
        // console.log(error)
        reject(error)
      })
  })
}

export function removeAccessToken() {
  return localStorage.removeItem(TokenKey)
}



