$(function(){
    window.onresize = function() {
		/*lineHeight();*/
		/*导航栏滚动高度变换导航栏*/
		// headerFixed()
		getTop();
		/*自适应导航栏变换*/
		// navbarToggle();

	}

	// navbarToggle();
	// headerFixed()
    getTop();
	
})

function getTop() {
	var top = $(document).scrollTop();
	if($(document).scrollTop() > 600) {

		$("#header-container-fixed").addClass("animated");
	} else {
		$("#header-container-fixed").removeClass("animated");
	}
	setTimeout(getTop);
}

function headerFixed() {
	if(document.body.offsetWidth <= 600) {
		$("#header-container-fixed").hide();
	} else {
		$("#header-container-fixed").show();
	}
}

