<template>
  <div id="marketingPlan">
    <!-- 头部 -->
    <header-mobile v-show="!isPc"></header-mobile>
    <header-two v-show="isPc"></header-two>

    <div class="main">
      <!-- banner -->
      <div class="bannerImg" v-if="!isPc">
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(item,index) in bannerList" :key="index">
            <img :src="item.imageUrl">
          </van-swipe-item>
        </van-swipe>
      </div>

      <div class="elementBanner" v-if="isPc">
        <el-carousel trigger="click" :height="height">
          <el-carousel-item v-for="(item,index) in bannerList" :key="index">
            <img class="bannerImgDetails" :src="item.imageUrl">
          </el-carousel-item>
        </el-carousel>
      </div>
      <!-- banner结束 -->

      <!-- 营销内容 -->
      <div class="content photoList">
        <div class="topTitle">
          <img src="../../img/icon_elememt@2x.png">
          <p class="titleText">营销内容</p>
        </div>
            <div class="details row" v-for="(item,index) in contentList" :key="index">
              <div class="leftText col-md-6 col-xs-12">
                {{item.imageTitle}}
                <div>
                  {{item.imageDesc}}
                </div>
              </div>
              <div class="rightImg col-md-6 col-xs-12">
                <img :src="item.imageUrl">
              </div>
            </div>
      </div>

      <!-- 创意服务 -->
      <div class="service photoList">
        <div class="topTitle">
          <img src="../../img/icon_elememt@2x.png">
          <p class="titleText">创意服务</p>
        </div>
        <div class="detailsImg-box">
          <img v-for="(item,index) in serviceList" :key="index" class="detailsImg" :src="item.imageUrl">
        </div>
        
      </div>

    </div>
    <!-- 底部 -->
    <footer-two></footer-two>
  </div>
</template>

<script>
  import {
    initShare
  } from '@/utils/wechatShare.js';
  import headerTwo from "../../components/header2";
  import headerMobile from "../../components/headerMobile";
  import footerTwo from "../../components/footer2";
  import { Loading } from "element-ui";


  export default {
    data() {
      return {
        bannerList: [],
        isPc: true,
        height: 0,
        contentList:[],
        serviceList:[]
      }
    },
    beforeMount() {
      //   动态获取element-ui 的banner组件高度  
      this.height = $(window).width() * 940 / 1920 + 'px';
    },
    mounted() {
      Loading.service();
      this.setWidth();
      this.animate();
      this.getBanner().finally(()=>{
        this.getContent().finally(()=>{
          this.getService()
          Loading.service().close();

        })
      });

      // 微信分享
      const shareData = {
        title: '360度O2O策略创意产业链整合',
        desc: '中旌影视传媒',
        link: location.href,
        imgUrl: 'https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/marketingPlan111.png',
        signUrl: encodeURIComponent(location.href)
      }
      initShare(shareData)
    },
    methods: {
      setWidth() {
        var widNum = $(window).width();
        if (widNum > 990) {
          this.clientType = '';
          this.isPc = true;
        } else {
          this.clientType = 'MOBILE';
          this.isPc = false;
        }
      },
      getBanner() {
        var self = this;
        var params = new URLSearchParams();
        // params.append("clientType", this.clientType);
        return self
          .$request({
            url: "/marketing/planning/getBannerList",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response);
            this.bannerList = response.results;
          })
          .catch(error => {
            console.log(error);
          });
      },
      getContent() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "/marketing/planning/getMarketingContent",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response);
            this.contentList = response.results.details;
          })
          .catch(error => {
            console.log(error);
          });
      },
      getService() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "/marketing/planning/getCreativeServices",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response);
            this.serviceList = response.results.details;
          })
          .catch(error => {
            console.log(error);
          });
      },
      //动画特效
        animate() {
          var a, b, c;
          a = $(window).height();
          $(window).scroll(function() {
            var b = $(this).scrollTop();
            $(".main .photoList").each(function() {
              c = $(this).offset().top;
              if(a + b > c) {
                $(this).find(".rightImg img,.detailsImg-box img").addClass("title-animate");
                setTimeout(() => {
                  $(this).find(".leftText").addClass("title-animate");
                }, 500)
              }
            });
          });
        }
    },
    components: {
      headerTwo,
      headerMobile,
      footerTwo
    }

  }

</script>

<style>
  /* banner开始 */
  .bannerImg img {
    width: 100%;
  }

  .elementBanner img {
    width: 100%;
  }

  .el-carousel__button {
    width: 8px;
    border-radius: 50%;
    height: 8px;
  }

  .el-carousel__indicators--horizontal {
    bottom: 15px;
  }

  .el-carousel__button {
    background-color: #fff;
    border: solid 1px #fff;
    opacity: 1;
  }

  .el-carousel__indicator.is-active button {
    background-color: transparent !important;
    border: solid 1px #fff;
  }

  element.style {
    display: none;
  }

  .el-carousel__arrow--left {
    left: 16px;
  }

  .el-carousel__arrow {
    display: none !important;
  }

  /* banner结束 */
  .topTitle {
    text-align: center;
    padding: 50px 0 40px 0;
  }

  .topTitle img {
    width: 40px;
    margin-bottom: 10px;
  }

  .titleText {
    color: #1D1D1D;
    font-size: 24px;
    font-weight: 500;
    margin: 0;
  }

  .rightImg img {
    width: 100%;
  }

  .leftText,.rightImg img,.detailsImg-box img{
    opacity: 0;
    transform: translateY(50px);
    transition: all 1s ease;
  }
  .rightImg {
    padding: 0;
  }
.leftText,
  .leftText {
    padding: 40px 40px 10px 40px;
    font-size: 24px;
    line-height: 40px;
    color: #fff;
  }

 .details {
    background: #C0812A;
    margin: 0 15%;
    margin-bottom: 35px;
  }

  .leftText div {
    margin-top: 10px;
    font-size: 18px;
    line-height: 36px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }

  .detailsImg {
    width: 100%;
    margin-bottom: 100px;
  }

  @media screen and (min-width: 1200px) and (max-width: 1500px) {
    .leftText {
      padding: 30px 40px 10px 40px;
      font-size: 30px;
      line-height: 30px;
      color: #fff;
    }

    .leftText div {
      margin-top: 10px;
      font-size: 18px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 4;
      overflow: hidden;
    }
  }

  @media screen and (min-width: 990px) and (max-width: 1200px) {
    .leftText {
      padding: 20px 30px 10px 30px;
      font-size: 24px;
      line-height: 25px;
      color: #fff;
    }

    .leftText div {
      margin-top: 10px;
      font-size: 16px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 4;
      overflow: hidden;
    }
  }

  @media screen and (max-width: 990px) {
    .leftText {
      padding: 20px 15px;
      font-size: 20px;
      line-height: 20px;
      color: #fff;
    }

    .leftText div {
      margin-top: 10px;
      font-size: 16px;
      display: block;
      line-height: 28px;
    }

    .details {
      background: #C0812A;
      margin: 0 5%;
      margin-top: 0px;
    }

    .topTitle {
      text-align: center;
      padding: 30px 0;
    }
    .detailsImg{
      margin-bottom: 30px;
    }
    .titleText {
      color: #1D1D1D;
      font-size: 22px;
      font-weight: 500;
      margin: 0;
    }

    .topTitle img {
      width: 28px;
      margin-bottom: 6px;
    }
  }

</style>



