
import crypto from 'crypto-js'
const key = 'IlEs^BewEUvoJBRFE^z0qyfytjvve6!e'

export function getSign(url, params, timeSpan, accessToken) {
  if (params) {
    url += '?' + params
  }
  if (url.toString().indexOf('?') > 0) {
    if (accessToken) {
      url += '&access_token=' + accessToken
    }
    if (timeSpan) {
      url += '&time_span=' + timeSpan
    }
  } else {
    if (accessToken) {
      url += '?access_token=' + accessToken
    }
    if (timeSpan) {
      url += '?time_span=' + timeSpan
    }
  }
  var sign = crypto.HmacSHA1(url.toLowerCase(), key)
  return sign.toString()
}



