import Vue from 'vue'
import Router from 'vue-router'
import aboutUs from '@/page/aboutUs/aboutUs'
import cooperation from '@/page/cooperation/cooperation'
import videoCase from '@/page/videoCase/videoCase'
import cultureCreativity from '@/page/cultureCreativity/cultureCreativity'

import industryLeader from "@/page/industryLeader/industryLeader"
import productCase from "@/page/productCase/productCase"

import index from "@/page/index/index"
import videoMaking from "@/page/videoMaking/videoMaking"
import activityExecution from "@/page/activityExecution/activityExecution"
import activityDetails from "@/page/activityExecution/activityDetails"
import marketingPlan from "@/page/marketingPlan/marketingPlan"


Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index',
      meta: {
        title: '西安中旌影视传媒股份有限公司'
      }
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      meta: {
        title: '西安中旌影视传媒股份有限公司'
      }
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: aboutUs,
      meta: {
        title: '关于我们'
      }
    },
    {
      path: '/cooperation',
      name: 'cooperation',
      component: cooperation,
      meta: {
        title: '战略合作资源'
      }
    },
    {
      path: '/videoCase',
      name: 'videoCase',
      component: videoCase,
      meta: {
        title: '视频案例'
      }
    },
    {
      path: '/cultureCreativity',
      name: 'cultureCreativity',
      component: cultureCreativity,
      meta: {
        title: '文化创意平台'
      }
    },
    {
      path: '/industryLeader',
      name: 'industryLeader',
      component: industryLeader,
      meta: {
        title: '行业领军资源'
      }
    },
    {
      path: '/productCase',
      name: 'productCase',
      component: productCase,
      meta: {
        title: '产品开发案例'
      }
    },
    {
      path: '/videoMaking',
      name: 'videoMaking',
      component: videoMaking,
      meta: {
        title: '影视制片制作'
      }
    },
    {
      path: '/marketingPlan',
      name: 'marketingPlan',
      component: marketingPlan,
      meta: {
        title: '营销策划'
      }
    },
    {
      path: '/activityExecution',
      name: 'activityExecution',
      component: activityExecution,
      meta: {
        title: '活动执行'
      },
      children:[{
        path: 'activityDetails',
      name: 'activityDetails',
      component: activityDetails,
      meta: {
        title: '活动执行详情'
      }
      }]
    }
  ]
})



