<template>
  <div id="cooperation">
    <!-- 头部 -->
    <header-mobile v-show="!isPc"></header-mobile>
    <header-two v-show="isPc"></header-two>
    <!--中间内容区域-->
    <div class="main">
      <div class="bannerImg">
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(item,index) in bannerList" :key="index">
            <img :src="item.imageUrl">
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="infoBox">
        <div class="topContent">
          <div class="textDetail">
            西安中旌影视传媒股份有限公司自成立以来就秉承着“中和雍睦，旌智明德”的企业精神，历经多次改革创新，整合行业内优质资源，发挥自身专业优势，将传统影视文化与现代影视工艺相结合，打造新型的影视文化产业链。影视方面，业务覆盖影视投资制作与发行、影视栏目策划、影视宣传片拍摄、影视后期服务、艺人经纪与管理，同时搭建大学生及年轻人的文化创意“互联网+影视”服务平台，配以校园传媒矩阵三大品牌营销体系，将业务延展到品牌营销管理、营销传播推广策划、新媒体运营等多维领域，致力于将有态度的人才和有温度的作品传送到整个影视传媒行业，让中旌影视传媒成为中国影视传媒行业的一面旌旗。
          </div>
        </div>

        <div class="photoList">
          <div class="topTitle">
            <img src="../../img/icon_elememt@2x.png">
            <p class="titleText">战略合作资源</p>
            <p class="titleEn">Our Partners</p>
          </div>
          <div class="photoBox row" v-for="(item,index) in showImgList" :key="index">
            <div class="companyList animateList" v-if="item.code=='ENTERPRISE'">
              <p>{{item.description}}</p>
              <div class="col-md-2 col-xs-4 imgBox" v-for="(item, index) in (item.details)" :key="index">
                <img :src="item.imageUrl">
              </div>
              <div class="clearfix"></div>
            </div>

            <div class="mediaList animateList" v-if="item.code!='ENTERPRISE'">
              <p>{{item.description}}</p>
              <div class="col-md-2 col-xs-4 videoBox" v-for="(item, index) in (item.details)" :key="index">
                <div class="videoImgBox">
                  <img :src="item.imageUrl">
                </div>
                <p>{{item.imageTitle}}</p>
              </div>
              <div class="clearfix"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 底部 -->
    <footer-two></footer-two>
  </div>
</template>

<script>
  import {
    initShare
  } from '@/utils/wechatShare.js'
  import { Loading } from 'element-ui';

  // import { Swipe, SwipeItem } from 'vant';
  import headerTwo from "../../components/header2";
  import headerMobile from "../../components/headerMobile";
  import footerTwo from "../../components/footer2";
  import "../../common/js/cooperation.js";
  export default {
    data() {
      return {
        showImgList: [],
        bannerList: [],
        clientType: '',
        isPc: true
      };
    },
    mounted() {
        Loading.service();
      this.setWidth();
        this.getShowImg().finally(() => {
          this.getBanner();
          Loading.service().close();

        });
        
      const shareData = {
        title: '“旌”细服务+“旌”湛业务',
        desc: '中旌影视传媒',
        link: location.href,
        imgUrl: 'https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/cooper111.png',
        signUrl: encodeURIComponent(location.href)
      }
      initShare(shareData)

    },
    methods: {
      setWidth() {
        var widNum = $(window).width();
        if (widNum > 990) {
          this.clientType = '';
          this.isPc = true;
        } else {
          this.clientType = 'MOBILE';
          this.isPc = false;
        }
      },
      getShowImg() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "cooperate/resources/getShowImageList",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response);
            this.showImgList = response.results;
          })
          .catch(error => {
            console.log(error);
          });
      },
      getBanner() {
        var self = this;
        var params = new URLSearchParams();
        params.append("clientType", this.clientType);
        return self
          .$request({
            url: "cooperate/resources/getBannerList",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response);
            this.bannerList = response.results;
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    components: {
      headerTwo,
      footerTwo,
      headerMobile
    }
  };

</script>

<style scoped>
  @import "../../common/css/cooperation.css";

  .videoBox p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
  }

  .infoBox {
    margin-bottom: 100px;
  }

  .bannerImg img {
    width: 100%;
  }

  @media screen and (max-width: 992px) {
    .bannerImg img {
      width: 200% !important;
      margin-left: -50%;
    }
  }

</style>



