$(function(){
	$(".icon_elememt img").hover(function(){
		$(this).attr('src',require('../../img/icon_elememt1.png'))
	},function(){
		$(this).attr('src',require('../../img/icon_elememt.png'))
	})
	//页面刷新回到顶部刷新数据
	$(window).scrollTop(0);
	//动画特效
	animate();
})

//动画特效
function animate() {
	var a, b, c;
	a = $(window).height();
	$(window).scroll(function() {
		var b = $(this).scrollTop();
		$(".main-container .animateList").each(function() {
			c = $(this).offset().top+100;
			if(a + b > c) {
				setTimeout(()=>{
					$(this).find(".cultureTitle,.ultureText").addClass("title-animate");
				},500)
				setTimeout(()=>{
					$(this).find(".cultureImg img,.cultureImg img").addClass("title-animate");
				},1000)
				setTimeout(()=>{
					$(this).find(".mobileImg img").addClass("title-animate");
				},2500)
			}
		});
	});
}


