import axios from 'axios'
// import { Message } from 'element-ui'
// import store from '@/store'
import { getAccessToken, refreshAccessToken } from '@/utils/accessToken'
import { getSign } from '@/utils/signUtil'

// 创建axios实例
const service = axios.create({
  baseURL: process.env.BASE_API, // api的base_url
  timeout: 25000 // 请求超时时间
})

// request拦截器
service.interceptors.request.use(config => {
  return new Promise((resolve, reject) => {
    getAccessToken().then(data => {
      const time_span = (new Date()).valueOf()
      config.headers['version_code'] = '1'
      config.headers['client_type'] = 'WEB'
      config.headers['access_token'] = data.access_token
      config.headers['time_span'] = time_span
      config.headers['sign'] = getSign(config.baseURL + config.url, config.params.toString(), time_span, data.access_token)
      resolve(config)
    }).catch(error => {
      console.log(error) // for debug
      reject(error)
    })
  })
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone拦截器
service.interceptors.response.use(
  response => {
    const res = response.data
    // AccessToken过期
    if (res.code === 40307) {
      return new Promise((resolve, reject) => {
        getAccessToken().then(data => {
          refreshAccessToken(data.refresh_token).then(res => {
            console.log('重新发送请求' + response.config.url)
            const time_span = (new Date()).valueOf()
            response.config.headers['access_token'] = res.access_token
            response.config.headers['time_span'] = time_span
            const params = response.config.params.toString() || response.config.data.toString()
            response.config.headers['sign'] = getSign(response.config.baseURL + response.config.url, params, time_span, res.access_token)
            axios(response.config).then(result => {
              console.log('重新发送请求成功')
              resolve(result.data)
            }).catch(error => {
              reject(error)
            })
          }).catch(error => {
            console.log(error) // for debug
            reject(error)
          })
        }).catch(error => {
          console.log(error) // for debug
          reject(error)
        })
      })
    }
    if (res.code !== 200 || res.hasError) {
      // Message({
      //   message: res.message,
      //   type: 'error',
      //   duration: 5 * 1000
      // })

      //  40302:userToken 过期了或用户未登录;
      if (res.code === 40302) {
        // store.dispatch('FedLogOut').then(() => {
        //   location.reload()// 为了重新实例化vue-router对象 避免bug
        // })
      }
      return Promise.reject('error')
    } else {
      return response.data
    }
  },
  error => {
    console.log('err' + error)// for debug
    // Message({
    //   message: error.message,
    //   type: 'error',
    //   duration: 5 * 1000
    // })
    return Promise.reject(error)
  }
)

export default service



