      <div class="infoBox">
        <div class="topContent row">
          <div class="topContentLeft col-md-6">
            <p>企业精神：</p>
            <p class="textInfo">中和雍睦 旌智明德</p>
            <p>企业文化：</p>
            <p class="textInfo">敬天爱人 知行合一</p>
          </div>
          <div class="topContentRight col-md-6">
            <span>西安中旌影视传媒股份有限公司</span> 成立于2015年12月，企业位于陕西省西安市曲江新区汇新路以东曲江国际金融中心商务区，带着打造匠心精品的初心在古城西安落地生根。
          </div>
          <div class="clearfix"></div>
          <div
            class="textDetail"
          >西安中旌影视传媒股份有限公司自成立以来就秉承着“中和雍睦，旌智明德”的企业精神，历经多次改革创新，整合行业内优质资源，发挥自身专业优势，将传统影视文化与现代影视工艺相结合，打造新型的影视文化产业链。影视方面，业务覆盖影视投资制作与发行、影视栏目策划、影视宣传片拍摄、影视后期服务、艺人经纪与管理，同时搭建大学生及年轻人的文化创意“互联网+影视”服务平台，配以校园传媒矩阵三大品牌营销体系，将业务延展到品牌营销管理、营销传播推广策划、新媒体运营等多维领域，致力于将有态度的人才和有温度的作品传送到整个影视传媒行业，让中旌影视传媒成为中国影视传媒行业的一面旌旗。</div>
        </div>

        <div class="photoList">
          <div class="topTitle">
            <img src="../../img/icon_elememt@2x.png">
            <p class="titleText">专业的团队</p>
            <p class="titleEn">Proffessional Team</p>
          </div>
