              <img class="gray" :src="item.imgUrl">
            </div>
           
          </div>
        </div>

        <div class="photoList">
          <div class="topTitle">
            <img src="../../img/icon_elememt@2x.png">
            <p class="titleText">联系我们</p>
            <p class="titleEn">Contant Us</p>
          </div>
          <div class="photoBox row mapBox">
            <div class="col-md-12 dituBox">
              <div id="dituContent"><baidu-map></baidu-map></div>
              <!-- <map></map> -->
              <div class="rightContactInfo">
                <div class="iconBox">
                  <img src="../../img/homepage_footer_icon_mail@2x.png">
