<script>
import headerTwo from "../../components/header2";
import headerMobile from "../../components/headerMobile";
import footerOne from "../../components/footer1";
import baiduMap from '../../components/map';
import "../../common/js/aboutUs.js";
export default {
  data() {
    return {
      componyImg:[
        {
          imgUrl:require("../../img/guanyuwomen_pic001.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic0002.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic003.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic004.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic005.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic006.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic007.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic008.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic009.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic010.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic011.png")
        },
        {
          imgUrl:require("../../img/guanyuwomen_pic012.png")
        }
      ],
      isPc:true
    };
  },
  mounted(){
this.setWidth()
  },
  components:{
    baiduMap,
    footerOne,
    headerTwo,
    headerMobile
  },
  methods:{
    setWidth(){
      var widNum = $(window).width();
      if (widNum > 990) {
        this.clientType = '';
        this.isPc=true
      } else {
        this.clientType = 'MOBILE';
        this.isPc=false
      }
    },
    setImgGray(a){
      $('.imgBox').eq(a).children('img').removeClass('gray');
		// $('.imgBox').eq(a).siblings('.imgBox').children('img').addClass('gray')
    },
    setImgNomal(a){
      $('.imgBox').eq(a).children('img').addClass('gray');
		// $('.imgBox').eq(a).siblings('.imgBox').children('img').addClass('gray')
    }
  }
};
</script>

<style scoped>
@import "../../common/css/aboutUs.css";
</style>


