$(function() {
//页面刷新回到顶部刷新数据
$(window).scrollTop(0);
//动画特效
if($(window).width() > 990) {
	animate();
}

	
	//窗口滚动和左边菜单联动
	change_size();
	$(window).on('resize', function(event) {
		event.preventDefault();
		change_size();
	});
	

	/***滚动鼠标**/
	

})

//滚动代码
var $key = 0; //记录滚动到哪一个
var canscroll = true; //用来限制滚动频率

//动态设置大小
function change_size() {
	var window_height = $(window).height();
	$(".leftTab").height(window_height - 120);
	var size = Number($(".leftTab ul li").size());
	$("body,html").stop().animate({
		"scrollTop": $(".infoBox>div").eq($key).offsetTop
	}, 800, function() {
		//释放滚动条
		canscroll = true;
	});
	//添加菜单当前色
	$(".leftTab ul li").eq($key).addClass('activeLi').siblings().removeClass('activeLi');
}

//动画特效
function animate() {
	var a, b, c;
	a = $(window).height();
	$(window).scroll(function() {
		var b = $(this).scrollTop();
		$(".infoBox .photoList").each(function(i) {
			c = $(this).offset().top;
			if(a + b > c) {
				setTimeout(() => {
				$(this).find(".videoLabel,.videoList,.serviceItem ,.videoBox,.videoTitle,.imgBoxCoo,.imgBox img,.videoItem img,.rightContactInfo").addClass("title-animate");
			}, 500)
			}
		});
		$(".photoBox .animateList").each(function() {
			c = $(this).offset().top;
			if(a + b > c) {
				
				$(this).find("p,.videoImgBox img").addClass("title-animate");
			
			}
		});
	});
}





