<template>
  <div id="industryLeader">
    <!-- 头部 -->
    <header-mobile v-show="!isPc"></header-mobile>
    <header-two v-show="isPc"></header-two>
    <!--中间内容区域-->
    <div class="main">
      <div class="productList">
        <div class="productBox row" v-for="(item,index) in productCase" :key="index">
          <div class="productImg col-md-4">
            <img :src="item.imgUrl">
          </div>
          <div class="productInfo col-md-8">
            <div class="productTitle">{{item.title}}</div>
            <div class="productText">{{item.description}}</div>
          </div>
        </div>
      </div>
      <!-- 分页器 -->
      <div class="pageNumBox">
        <el-pagination
          background
          :page-size="this.pageSize"
          layout="prev, pager, next"
          :current-page.sync="pageNum"
          @current-change="handleCurrentChange"
          :total="this.total"
        ></el-pagination>
      </div>
    </div>
    <!-- 底部 -->
    <footer-two></footer-two>
  </div>
</template>

<script>
import { initShare } from "@/utils/wechatShare.js";

import headerTwo from "../../components/header2";
import headerMobile from "../../components/headerMobile";
import footerTwo from "../../components/footer2";
export default {
  data() {
    return {
      productCase: [],
      pageSize: 0,
      pageNum: 1,
      total: 0,
      isPc: true
    };
  },
  mounted() {
    this.setWidth();
    this.getProductCase();
    const shareData = {
      title: "以营销策划为龙头，创意表现为主导，影视制作为特长",
      desc: "中旌影视传媒",
      link: location.href,
      imgUrl:
        "https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/product111.png",
      signUrl: encodeURIComponent(location.href)
    };
    initShare(shareData);
  },
  created() {
    this.pageNum = Number(sessionStorage.getItem('ProductPageNum')) || 1;
},
  methods: {
    setWidth() {
      var widNum = $(window).width();
      if (widNum > 990) {
        this.isPc = true;
      } else {
        this.isPc = false;
      }
    },
    getProductCase() {
      var self = this;
      var params = new URLSearchParams();
      params.append("pageNum", this.pageNum);
      return self
        .$request({
          url: "product/case/getProductCaseList",
          method: "get",
          params: params
        })
        .then(response => {
          // console.log(response);
          this.productCase = response.results;
          this.total = response.extraInfo.totalRecords;
          this.pageSize = response.extraInfo.pageSize;
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleCurrentChange(pageNum) {
      this.productCase=[];
      this.pageNum = pageNum;
      sessionStorage.setItem('ProductPageNum',pageNum)
      this.getProductCase();
    }
  },
  components: {
    headerTwo,
    footerTwo,
    headerMobile
  }
};
</script>

<style scoped>
@import "../../common/css/productCase.css";
.main {
  background: #f7f7f7 !important;
  padding: 4% 0;
}
.el-pagination {
  text-align: center;
}
.pageNumBox {
  margin: 50px 0;
}
</style>


