<template>
  <div id="headerMobile">
    <div id="nav">
      <div class="navbar-small" >
        <img class="menuImg" src="../img/menuIcon002.png" @click="menu()">
        <div class="aa" v-show="this.menuflag">
          <div class="leftTab" v-show="this.menuflag">
            <img class="closeImg" src="../img/closeImg02.png" @click="closeMenu()">
            <ul>
              <li v-for="(item,index) in headerList" :key="index" tag="li" exact>
                <router-link :to="item.rouPath" active-class="color222">
                  — {{item.headerName}}
                  </router-link>
              </li>
            </ul>
          </div>
          <transition name="fade"><div class="bgMask" v-show="this.menuflag"></div></transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../common/js/header.js";

export default {
  data() {
    return {
      isactive: 0,
      menuflag: false,
      headerList: [
        { rouPath: "/cultureCreativity", headerName: "文化创意平台" },
        { rouPath: "/videoMaking", headerName: "影视制片制作" },
        { rouPath: "/marketingPlan", headerName: "营销策划" },
        { rouPath: "/activityExecution", headerName: "活动执行" },
        { rouPath: "/industryLeader", headerName: "行业领军资源" },
        { rouPath: "/productCase", headerName: "产品开发案例" },
        { rouPath: "/cooperation", headerName: "战略合作资源" },
        { rouPath: "/aboutUs", headerName: "关于我们" }
      ]
    };
  },
  mounted() {
    this.menuflag = false;
     this.$router.afterEach((to, from, next) => {
        window.scrollTo(0, 0)
    })
    // console.log(this.menuflag)
  },
  methods: {
    fn(index) {
      this.isactive = index;
    },
    menu() {
      this.menuflag = true;
      // console.log(this.menuflag)
    },
    closeMenu(){
      this.menuflag = false;
    }
  }
};
</script>

<style scoped>
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  transition: opacity 3s;
}
.fade-leave-to {
  opacity: 0;
}
.fade-leave-active {
  transition: opacity 3s;
}
/*header*/
.color222 {
  color: #000 !important;
}
.navbar-default {
  background-color: transparent;
  border-color: transparent;
}
.navbar-toggle {
  float: left;
}
#header-container a {
  color: #fff !important;
}
#header-container-fixed a {
  color: #2f2f2f !important;
}
.navbar-small a {
  color: #888;
}
a {
  text-decoration: none;
}
.navbar-default .navbar-toggle:focus,
.navbar-default .navbar-toggle:hover {
  background-color: #cba365;
}

.navbar-default .navbar-toggle {
  background-color: #cba365;
}

.navbar-default .navbar-toggle {
  border-color: #cba365;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: #fff;
}

#codereplayermenu {
}

.navbar-nav {
  display: inline-block;
  zoom: 1;
  padding: 0px 0px 5px 0;
  vertical-align: middle;
}

/*.navbar-nav>li {
    padding-top: 10px;
}*/

.s-font-body {
  color: #fff;
  font-size: 16px;
}

.navbar-left img {
  width: 90px;
}

.nav > li > a {
  padding: 40px 22px 20px 22px;
}

.navbar-left {
  padding-left: 12px;
}

.navActive1 {
  background: rgba(236, 236, 236, 1);
  box-sizing: border-box;
  border-top: 4px solid #c89f62;
}

.navActive1 .s-nav-item {
  padding-top: 36px;
}

.navActive {
  background: rgba(236, 236, 236, 0.2);
}
#header-container li > a:hover {
  background: rgba(236, 236, 236, 0.2);
}
#header-container {
  position: absolute;
  z-index: 9999;
  top: 0;
  width: 100%;
  text-align: center;
}

#header-container-fixed {
  background: #fff;
}

#header-container-fixed {
  background: white;
  position: fixed;
  z-index: 210;
  top: -125px;
  opacity: 0;
  left: 0;
  right: 0;
  padding: 0;
  padding-top: 0px;
  pointer-events: none;
  -webkit-transform: scale(1.065);
  transform: scale(1.065);
  transition: all 0.75s cubic-bezier(0.7, 0, 0.3, 1);
  overflow: visible;
}
#header-container-fixed li > a:hover {
  background: rgba(236, 236, 236, 1);
  box-sizing: border-box;
}
#header-container-fixed.animated {
  opacity: 1;
  top: -1px;
  -webkit-transform: none;
  transform: none;
  pointer-events: all;
}

.s-font-body1 {
  font-size: 16px;
}
.container {
  padding: 0 !important;
}
#header-container {
  padding: 0 0 25px 0;
  -webkit-animation-name: navDown;
  animation-name: navDown;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s;
  -webkit-animation-timing-function: ease-out;
  animation-timing-function: ease-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  z-index: 9998 !important;
}
#header-container .nav-center {
  min-width: 150px;
}
#header-container .nav-item,
#header-container-fixed .nav-item {
  display: inline-block;
  zoom: 1;
  padding: 10px 22px;
  vertical-align: middle;
}

.bgMask {
  background: #000;
  opacity: 0.7;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 101;
}

@media screen and (max-width: 1570px) {
}

@media screen and (max-width: 1400px) {
}

@media screen and (max-width: 1200px) {
  .s-font-body,
  .s-font-body1 {
    font-size: 14px;
  }
  .nav > li > a {
    padding: 15px 17px 8px 17px;
  }
  .navActive1 .s-nav-item {
    padding-top: 11px;
  }
  .navbar-left img {
    width: 50px;
  }
}

@media screen and (max-width: 992px) {
  .s-font-body {
    font-size: 14px;
  }
  .navActive1 .s-nav-item {
    padding-top: 6px;
  }
  .nav > li > a {
    padding: 10px 6px;
  }
  .navbar-left img {
    width: 40px;
  }
  .navbar-left {
    padding-left: 5px;
  }
  .menuImg {
    width: 50px;
    position: fixed;
    top: 20px;
    left: 11px;
    display: block;
    z-index: 100;
  }
  .leftTab {
    position: fixed;
    top: 50%;
    margin-top: -210px;
    left: 50%;
    margin-left: -130px;
    z-index: 102;
    font-size: 16px;
    line-height: 12px;
    
  }
  .closeImg {
    width: 50px;
    position: absolute;
    top: -25px;
    right: -25px;
    display: block;
  }
  .leftTab ul {
    background: #fff;
    color: #979797;
    padding: 20px 70px;
  }
  .leftTab li {
    padding: 16px 0px;
  }
}
.a1,
.a2 {
  background: rgba(236, 236, 236, 0.2);
  box-sizing: border-box;
  border-top: 4px solid #c89f62;
}
</style>


