<template>
  <div id="videoMaking">
    <div class="containerleft-line"></div>
    <div class="containerright-line"></div>
    <!--中间内容区域-->
    <div class="main">
      <router-link to="/index">
        <img class="topLogoBox" src="../../img/logo2.png">
      </router-link>
      <img @click="showMenu()" class="menuImg" src="../../img/menuIcon001.png">
      <!--悬浮面板-->
      <div class="leftTab" v-show="this.menuFlag">
        <img @click="hideMenu()" class="closeImg" src="../../img/closeImg01.png">
        <ul>
          <li
            @click="goNext(index)"
            v-for="(item,index) in topLi"
            :key="index"
            class="activeLi topLi"
            v-if="index==0"
          >
            <span id="topInfo">{{item.liName}}</span>
            <img style="display: none;" class="topImg" src="../../img/fromthe top.png">
          </li>
          <li
            @click="goNext(index)"
            v-for="(item,index) in topLi"
            :key="index"
            v-if="index>0"
          >— {{item.liName}}</li>
        </ul>
      </div>
      <div class="bgMask" v-show="this.bgFlag"></div>
      <div v-show="videoBg">
        <div v-for="(item,index) in MovieCaseList" :key="index">
          <div class="video-position" v-show="activeIndex===item.caseId">
            <div class="video-play">
              <div class="video-video">
                <video :id="item.caseId" controls="controls" :poster="item.coverImgUrl">
                  <!-- <source :src="item.movieUrl" type="video/mp4"> -->
                </video>
              </div>
              <div class="video-close">
                <img src="../../img/close_001.png" @click="msgClose(item.caseId)">
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="infoBox">
        <div class="topImgBox row">
          <div
            class="leftContent col-xs-6 col-md-6"
            v-for="(item,index) in topInfo"
            :key="'a'+index"
            v-show="index%2==0"
          >
            <div class="lineY"></div>
            <img :src="item.imageUrl" v-if="index%2==0">
          </div>
          <div
            class="rightContent col-xs-6 col-md-6"
            v-for="(item,index) in topInfo"
            :key="'b'+index"
            v-show="index%2!=0"
          >
            <div class="toastInfoHide">
              <span style="display: none;" class="InfoText">zjys@zhongjingchina.cn</span>
              <img style="display: none;" class="codeImg" src="../../img/weixinfuwu _ii.png">
              <img style="display: none;" class="codeImg" src="../../img/weixindingyue_ iio.png">
            </div>
            <div class="toastInfo">
              <img
                @mouseenter="showTopInfo()"
                @mouseleave="hideTopInfo()"
                class="infoIcon1"
                src="../../img/wechat_icon_yuan_nor.png"
              >
              <img
                @mouseenter="showMailInfo()"
                @mouseleave="hideMailInfo()"
                class="infoIcon2"
                src="../../img/mail_icon_yuan_nor.png"
              >
            </div>
            <img class="goAli" @click="goAli()" :src="item.imageUrl" v-if="index%2!=0">
          </div>
          <div class="lineX"></div>
        </div>
        <!--业务内容-->
        <div class="photoList" id="a1">
          <div class="topTitle">
            <div class="titleHideInfo">
              业务内容
              <span>OUR SERVICES</span>
            </div>
            <img src="../../img/our services_i.png">
          </div>
          <div class="serviceBox">
            <div class="serviceItemBox row">
              <div
                @mouseenter="setImgGray(index)"
                @mouseleave="setImgNomal(index)"
                class="serviceItem col-xs-2 col-md-2"
                v-for="(item,index) in ourService"
                :key="index"
              >
                <p>{{item.imageTitle}}</p>
                <img class="gray" :src="item.imageUrl">
              </div>
              <div class="clearfix"></div>
            </div>
            <img src="../../img/our services_bg001.png">
          </div>
        </div>
        <!--核心优势-->
        <div id="centerTec" class="photoList centerTec">
          <div class="topTitle">
            <div class="titleHideInfo">
              核心优势
              <span>OUR ADVANTAGES</span>
            </div>
            <div class="rightLineAdvan"></div>
            <img src="../../img/our-advantage_001.png">
          </div>

          <!-- Swiper -->
          <div class="swiper-container">
            <div class="swiper-wrapper">
              <div
                class="swiper-slide"
                @mouseenter="setInfoShow(index)"
                @mouseleave="setInfoHide(index)"
                v-for="(item,index) in OurAdvantages"
                :key="index"
              >
                <div class="textBox">
                  <div class="textTitle">
                    {{item.imageTitle}}
                    <img src="../../img/arrow_icon11.png">
                    <div class="clearfix"></div>
                  </div>
                  <div class="textMore" style="display: none;">{{item.imageDesc}}</div>
                </div>
                <img class="swiperImg" :src="item.imageUrl">
              </div>
            </div>
          </div>
        </div>
        <!--案例展示-->
        <div class="photoList caseShow">
          <div class="topTitle">
            <div class="titleHideInfo">
              案例展示
              <span>PROJECT CASE</span>
            </div>
            <div class="rightLineProject"></div>
            <img src="../../img/peoject case_i.png">
          </div>
          <div class="photoBox row videoList">
            <div
              class="col-xs-12 col-md-4 videoItem"
              v-for="(item,index) in MovieCaseList"
              :key="index"
            >
              <div class="videoImgContentBox" @click="msgShow(item.caseId,item.movieUrl)">
                <div class="playImg">
                  <img src="../../img/play_001.png">
                </div>
                <img :src="item.coverImgUrl">
              </div>

              <div
                @mouseenter="titleRole(item.caseId)"
                :id="'title'+item.caseId"
                class="videoTitle" titleFlag="true"
              >
                <p class="titleAnimate">{{item.title}}</p>
              </div>
              <div class="videoLabel">
                <span
                  v-for="(item,index) in (item.labels)"
                  :key="index"
                  v-if="index<3"
                >#{{item.labelName}}</span>
              </div>
              <!-- <div class="video-position" v-show="activeIndex===index">
                <div class="video-play">
                  <div class="video-video">
                    <video :id="'vdo'+index" controls="controls" :poster="item.coverImgUrl">
                      <source :src="item.movieUrl" type="video/mp4">
                    </video>
                  </div>
                  <div class="video-close">
                    <img src="../../img/close_001.png" @click="msgClose(index)">
                  </div>
                </div>
              </div>-->
            </div>
          </div>
          <div @click="goVideoCase()" class="moreBtn">更多案例</div>
        </div>
        <!--服务流程-->
        <div class="photoList serviceFlow">
          <div class="topTitle">
            <div class="titleHideInfo">
              服务流程
              <span>SERVICE PROCESSES</span>
            </div>
            <div class="rightLineContact"></div>
            <img src="../../img/service processes_i.png">
          </div>
          <div class="photoBox row">
            <div
              v-for="(item,index) in flowList"
              :key="index"
              class="col-xs-6 col-md-4 col-lg-2 imgBox"
              @mouseenter="setBgShow(index)"
              @mouseleave="setBgHide(index)"
            >
              <div class="serviceItemInfo">
                <p class="serviceNum">{{index+1| changeNum()}}</p>
                <p>{{item.infoTitle}}</p>
                <p class="serviceDetail">{{item.infoContent}}</p>
              </div>
              <img class="infoImg" :src="item.arrowImg">
              <img class="bgImg" :src="item.bgImg">
            </div>
          </div>
        </div>
        <!--合作资源-->
        <div class="photoList contantUsBox">
          <div class="topTitle">
            <div class="titleHideInfo">
              合作资源
              <span>OUR PARTNERS</span>
            </div>
            <div class="rightLineCoo"></div>
            <img src="../../img/our partners_i.png">
          </div>
          <div class="photoListCoo">
            <div class="photoBoxCoo row" v-for="(item,index) in showImgList" :key="index">
              <div class="companyList" v-show="index==0">
                <p>{{item.description}}</p>
                <div
                  class="col-md-2 col-xs-4 imgBoxCoo"
                  v-for="(item, index) in (item.details)"
                  :key="index"
                >
                  <img :src="item.imageUrl">
                </div>
                <div class="clearfix"></div>
              </div>

              <div class="mediaList" v-show="index==1">
                <p>{{item.description}}</p>
                <div
                  class="col-md-2 col-xs-4 videoBox"
                  v-for="(item, index) in (item.details)"
                  :key="index"
                >
                  <div class="videoImgBox">
                    <img :src="item.imageUrl">
                  </div>
                  <p>{{item.imageTitle}}</p>
                </div>
                <div class="clearfix"></div>
              </div>
            </div>
          </div>
        </div>
        <!--联系我们-->
        <div class="photoList">
          <div class="topTitle">
            <div class="titleHideInfo">
              联系我们
              <span>CONTANT US</span>
            </div>
            <div class="rightLineContact"></div>
            <img src="../../img/contant us_i.png">
          </div>
          <div class="photoBox row mapBox">
            <div class="col-md-12 dituBox">
              <div id="dituContent">
                <baidu-map></baidu-map>
              </div>
              <div class="rightContactInfo">
                <div class="iconBox">
                  <img src="../../img/homepage_footer_icon_mail@2x.png">
                  <p>zjys@zhongjingchina.cn</p>
                </div>
                <div class="lineBox"></div>
                <div class="iconBox">
                  <img src="../../img/homepage_footer_icon_location@2x.png">
                  <p class="locationText">西安市曲江新区汇新路以东曲江国际金融中心 21 F</p>
                </div>
                <div class="lineBox"></div>
                <div class="iconBox">
                  <img src="../../img/homepage_footer_icon_phone@2x.png">
                  <p>029-89555118</p>
                </div>
              </div>
              <img src="../../img/homepage_footer_pic_bg@2x.jpg">
            </div>
            <div class="clearfix"></div>
          </div>
        </div>
      </div>
      <div class="photoBox">
        <!-- 底部-->
        
        <footer-three></footer-three>
      </div>
    </div>
  </div>
</template>

<script>
import { initShare } from "@/utils/wechatShare.js";
import baiduMap from "../../components/map";
import footerThree from "../../components/footer3";
import { Loading } from "element-ui";

import Swiper from "swiper";
import "../../common/js/jquery.mousewheel.min.js";
import "../../common/js/videoMaking.js";
// import "../../common/js/swiper.min.js";

// import Swiper from 'swiper';
import "swiper/dist/css/swiper.min.css";

export default {
  data() {
    return {
      OurAdvantages: [],
      showImgList: [],
      MovieCaseList: [],
      ourService: [],
      topInfo: [],
      viewNum: 4,
      menuFlag: false,
      bgFlag: false,
      flag: true,
      pc: true,
      activeIndex: -1,
      videoBg: false,
      titleFlag: true,
      topLi: [
        {
          liName: "Top"
        },
        {
          liName: "业务内容"
        },
        {
          liName: "核心优势"
        },
        {
          liName: "案例展示"
        },
        {
          liName: "服务流程"
        },
        {
          liName: "合作资源"
        },
        {
          liName: "联系我们"
        }
      ],
      flowList: [
        {
          infoTitle: "客户来电",
          infoContent: "双方通过电话或其他联系方式进行初步联络。",
          bgImg: require("../../img/liucheng_pic001.png"),
          arrowImg: require("../../img/arrow_left.png")
        },
        {
          infoTitle: "初步沟通",
          infoContent: "双方就合作需求进一步沟通，达成合作意向。",
          bgImg: require("../../img/liucheng_pic002.png"),
          arrowImg: require("../../img/arrow_middle.png")
        },
        {
          infoTitle: "创意梗概",
          infoContent: "输出创意梗概，明确内容、价格及进度排期。",
          bgImg: require("../../img/liucheng_pic003.png"),
          arrowImg: require("../../img/arrow_middle.png")
        },
        {
          infoTitle: "签订合同",
          infoContent: "签订委托合同书，明确双方负责人。",
          bgImg: require("../../img/liucheng_pic004.png"),
          arrowImg: require("../../img/arrow_middle.png")
        },
        {
          infoTitle: "交预付款",
          infoContent: "初步制定项目进度时间轴，委托方支付项目预付款。",
          bgImg: require("../../img/liucheng_pic005.png"),
          arrowImg: require("../../img/arrow_middle.png")
        },
        {
          infoTitle: "前期策划",
          infoContent: "成立项目小组，分析、沟通、项目讨论、进行提案。",
          bgImg: require("../../img/liucheng_pic006.png"),
          arrowImg: require("../../img/arrow_right.png")
        },
        {
          infoTitle: "确定方案",
          infoContent: "根据沟通对方案进行调整，最终方案客户确定满意。",
          bgImg: require("../../img/liucheng_pic007.png"),
          arrowImg: require("../../img/arrow_left.png")
        },
        {
          infoTitle: "中期拍摄",
          infoContent: "场景调研、筹备拍摄，导演分镜，正式拍摄。",
          bgImg: require("../../img/liucheng_pic008.png"),
          arrowImg: require("../../img/arrow_middle.png")
        },
        {
          infoTitle: "后期制作",
          infoContent: "后期制作进行素材剪辑及特效音乐调色包装。",
          bgImg: require("../../img/liucheng_pic009.png"),
          arrowImg: require("../../img/arrow_middle.png")
        },
        {
          infoTitle: "修改输出",
          infoContent: "与客户沟通并根据需求进行修改，成片输出。",
          bgImg: require("../../img/liucheng_pic010.png"),
          arrowImg: require("../../img/arrow_middle.png")
        },
        {
          infoTitle: "支付尾款",
          infoContent: "客户再次审片，修正确认客户满意，支付尾款。",
          bgImg: require("../../img/liucheng_pic011.png"),
          arrowImg: require("../../img/arrow_middle.png")
        },
        {
          infoTitle: "交付成品",
          infoContent: "客户签收，继续与客户保持联系，及时解决客户相关问题。",
          bgImg: require("../../img/liucheng_pic012.png"),
          arrowImg: require("../../img/arrow_right.png")
        }
      ],
      swiperOption: {
        slidesPerView: 4,
        spaceBetween: 15,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      swiperOption1: {
        slidesPerView: 2,
        spaceBetween: 15,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      }
    };
  },
  mounted() {
    Loading.service();
    this.getOurAdvantages().finally(() => {
      this.getShowImg().finally(() => {
        this.getMovieCaseList().finally(() => {
          this.getOurServices().finally(() => {
            this.getTopInfo();
            Loading.service().close();
          });
        });
      });
    });

    const shareData = {
      title: "以品质触动心灵,以诚意赢得市场,以专业缔造价值",
      desc: "中旌影视传媒",
      link: location.href,
      imgUrl:
        "https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/videoMaking111.png",
      signUrl: encodeURIComponent(location.href)
    };
    initShare(shareData);
  },
  updated() {
    this.$nextTick(function() {
      this.setNum();
      this.setItemA();
    });
  },
  methods: {
    goAli() {
      window.location.href = "https://mv.dashivlog.com/index";
    },
    setNum() {
      var widNum = $(window).width();
      var viewNum;
      if (widNum > 990) {
        this.bgFlag = false;
        this.menuFlag = true;
        this.viewNum = 4;
      } else {
        this.viewNum = 2;
      }
      var swiper = new Swiper(".swiper-container", {
        slidesPerView: this.viewNum,
        spaceBetween: 15,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      });
    },
    getOurAdvantages() {
      var self = this;
      var params = new URLSearchParams();
      return self
        .$request({
          url: "film/making/getOurAdvantages",
          method: "get",
          params: params
        })
        .then(response => {
          // console.log(response);
          this.OurAdvantages = response.results.details;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getShowImg() {
      var self = this;
      var params = new URLSearchParams();
      return self
        .$request({
          url: "cooperate/resources/getShowImageList",
          method: "get",
          params: params
        })
        .then(response => {
          // console.log(response);
          this.showImgList = response.results;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getMovieCaseList() {
      var self = this;
      var params = new URLSearchParams();
      return self
        .$request({
          url: "film/making/getMovieCaseList",
          method: "get",
          params: params
        })
        .then(response => {
          // console.log(response);
          this.MovieCaseList = response.results;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getOurServices() {
      var self = this;
      var params = new URLSearchParams();
      return self
        .$request({
          url: "film/making/getOurServices",
          method: "get",
          params: params
        })
        .then(response => {
          // console.log(response);
          this.ourService = response.results.details;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getTopInfo() {
      var self = this;
      var params = new URLSearchParams();
      return self
        .$request({
          url: "film/making/getTopInfo",
          method: "get",
          params: params
        })
        .then(response => {
          // console.log(response);
          this.topInfo = response.results.details;
        })
        .catch(error => {
          console.log(error);
        });
    },

    showTopInfo() {
      $(".infoIcon1").attr(
        "src",
        require("../../img/wechat_icon_yuan_pressed.png")
      );
      $(".codeImg").show();
    },
    hideTopInfo() {
      $(".infoIcon1").attr(
        "src",
        require("../../img/wechat_icon_yuan_nor.png")
      );
      $(".codeImg").hide();
    },
    showMailInfo() {
      $(".infoIcon2").attr(
        "src",
        require("../../img/mail_icon_yuan_pressed.png")
      );
      $(".InfoText").show();
    },
    hideMailInfo() {
      $(".infoIcon2").attr("src", require("../../img/mail_icon_yuan_nor.png"));
      $(".InfoText").hide();
    },
    setImgGray(a) {
      $(".serviceItem")
        .eq(a)
        .children("img")
        .removeClass("gray");
    },
    setImgNomal(a) {
      $(".serviceItem")
        .eq(a)
        .children("img")
        .addClass("gray");
    },
    setInfoShow(a) {
      $(".swiper-slide")
        .eq(a)
        .find(".textTitle")
        .children("img")
        .attr("src", require("../../img/arrow_icon22.png"));
      $(".swiper-slide")
        .eq(a)
        .find(".textMore")
        .slideDown();
    },
    setInfoHide(a) {
      $(".swiper-slide")
        .eq(a)
        .find(".textTitle")
        .children("img")
        .attr("src", require("../../img/arrow_icon11.png"));
      $(".swiper-slide")
        .eq(a)
        .find(".textMore")
        .slideUp();
    },
    setBgShow(a) {
      $(".imgBox")
        .eq(a)
        .find(".serviceDetail")
        .addClass("title-animate");
      $(".imgBox")
        .eq(a)
        .addClass("bgRed");
      $(".imgBox")
        .eq(a)
        .children(".bgImg")
        .addClass("opacityChange");
    },
    setBgHide(a) {
      $(".imgBox")
        .eq(a)
        .find(".serviceDetail")
        .removeClass("title-animate");
      $(".imgBox")
        .eq(a)
        .removeClass("bgRed");
      $(".imgBox")
        .eq(a)
        .children(".bgImg")
        .removeClass("opacityChange");
    },
    aaa() {
      var $key = 0; //记录滚动到哪一个
      var canscroll = true; //用来限制滚动频率
      $("#videoMaking").mousewheel(function(event, delta) {
        if (canscroll) {
          //限制滚动
          canscroll = false;
          $key -= delta;
          if ($key < 0) {
            $key = 0;
          } else if ($key > 7) {
            $key = 7;
            canscroll = true;
            return;
          }
        }
      });
    },
    //打开视频
    msgShow(e, x) {
      this.activeIndex = e;
      this.videoBg = true;
      this._dom = document.getElementById(e);
      this._dom.setAttribute("src", x);
      this._dom.play();
    },
    //关闭视频
    msgClose(e) {
      this.activeIndex = !e;
      this.videoBg = false;
      this._dom = document.getElementById(e);
      this._dom.pause();
      this._dom.setAttribute("src", "");
    },
    setItemA() {
      $(window).scroll(function() {
        // ($(".infoBox>div").eq(0).offset() || { "top": NaN }).top;
        let a1 =
          (
            $(".infoBox>div")
              .eq(0)
              .offset() || {
              top: NaN
            }
          ).top - 50;
        let a2 =
          (
            $(".infoBox>div")
              .eq(1)
              .offset() || {
              top: NaN
            }
          ).top - 50;
        let a3 =
          (
            $(".infoBox>div")
              .eq(2)
              .offset() || {
              top: NaN
            }
          ).top - 50;
        let a4 =
          (
            $(".infoBox>div")
              .eq(3)
              .offset() || {
              top: NaN
            }
          ).top - 50;
        let a5 =
          (
            $(".infoBox>div")
              .eq(4)
              .offset() || {
              top: NaN
            }
          ).top - 50;
        let a6 =
          (
            $(".infoBox>div")
              .eq(5)
              .offset() || {
              top: NaN
            }
          ).top - 50;
        let a7 =
          (
            $(".infoBox>div")
              .eq(6)
              .offset() || {
              top: NaN
            }
          ).top - 50;

        var b = $(this).scrollTop();
        if (b >= a7) {
          $(".leftTab ul li")
            .eq(6)
            .addClass("activeLi")
            .siblings()
            .removeClass("activeLi");
        } else if (b >= a2 && b < a3) {
          $(".leftTab ul li")
            .eq(1)
            .addClass("activeLi")
            .siblings()
            .removeClass("activeLi");
        } else if (b >= a3 && b < a4) {
          $(".leftTab ul li")
            .eq(2)
            .addClass("activeLi")
            .siblings()
            .removeClass("activeLi");
        } else if (b >= a4 && b < a5) {
          $(".leftTab ul li")
            .eq(3)
            .addClass("activeLi")
            .siblings()
            .removeClass("activeLi");
        } else if (b >= a5 && b < a6) {
          $(".leftTab ul li")
            .eq(4)
            .addClass("activeLi")
            .siblings()
            .removeClass("activeLi");
        } else if (b >= a6 && b < a7) {
          $(".leftTab ul li")
            .eq(5)
            .addClass("activeLi")
            .siblings()
            .removeClass("activeLi");
        } else if (b < a2) {
          $(".leftTab ul li")
            .eq(0)
            .addClass("activeLi")
            .siblings()
            .removeClass("activeLi");
        }

        if ($(window).width() > 990) {
          if (b < a2) {
            $(".topImg").hide();
            $("#topInfo").show();
            $(".leftTab ul li").css({
              background: "transparent",
              opacity: "1"
            });
            $(".leftTab").css("color", "#fff");
          } else {
            $(".topImg").show();
            $("#topInfo").hide();
            $(".leftTab").css("color", "#979797");
            $(".leftTab ul li").css({
              background: "#fff",
              opacity: "0.85"
            });
            $(".topLi").css({
              background: "transparent",
              "padding-left": "0"
            });
          }
        }
      });
    },
    goNext(a) {
      var $key = 0;
      $key = a;
      // $(".leftTab ul li").eq(a).addClass("activeLi").siblings().removeClass("activeLi");
      //执行动画
      if ($(window).width() > 990) {
        if ($key == 0) {
          $(".topImg").hide();
          $("#topInfo").show();
          $(".leftTab ul li").css({
            background: "transparent",
            opacity: "1"
          });
          $(".leftTab").css("color", "#fff");
        }
        //导航栏背景色显示
        if ($key != 0) {
          $(".topImg").show();
          $("#topInfo").hide();
          $(".leftTab").css("color", "#979797");
          $(".leftTab ul li").css({
            background: "#fff",
            opacity: "0.85"
          });
          $(".topLi").css({
            background: "transparent",
            "padding-left": "0"
          });
        }
      }
      if ($(window).width() < 990) {
        $(".leftTab,.bgMask").fadeOut();
      }
      $("body,html")
        .stop()
        .animate(
          {
            scrollTop: $(".infoBox>div")
              .eq(a)
              .offset().top
          },
          800
        );
    },
    showMenu() {
      this.menuFlag = true;
      this.bgFlag = true;
      // console.log(this.menuFlag);
      $(".leftTab,.bgMask").show();
    },
    hideMenu() {
      this.menuFlag = false;
      this.bgFlag = false;
    },
    goVideoCase() {
      // this.$router.push({ path: '/videoCase'})
      let routeUrl = this.$router.resolve({
        path: "/videoCase"
      });
      window.open(routeUrl.href, "_blank");
    },
     titleRole(e) {
      let titleDom = document.getElementById("title" + e);
      let titelWidth=titleDom.firstChild.offsetWidth;
      let parentWidth =titleDom.offsetWidth;
      if (titelWidth >= parentWidth && titleDom.getAttribute("titleFlag")) {
        titleDom.setAttribute("titleFlag", false);
        titleDom.firstChild.classList.add("titleMove");
        titleDom.classList.add("overInitial");
        setTimeout(() => {
          titleDom.firstChild.classList.remove("titleMove");
          titleDom.classList.remove("overInitial");
          titleDom.setAttribute("titleFlag", true);
        }, 8000);
      }
    }
  },
  components: {
    baiduMap,
    footerThree
  }
};
</script>

<style scoped>
@import "../../common/css/videoMaking.css";

.titleMove {
  animation: 8s wordsLoop linear infinite normal;
}

@keyframes wordsLoop {
  0% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
  }
  50% {
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
  }
  50.01% {
    transform: translateX(100%);
    -webkit-transform: translateX(100%);
  }
  100% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
  }
}

@-webkit-keyframes wordsLoop {
  0% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
  }
  50% {
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
  }
  50.01% {
    transform: translateX(100%);
    -webkit-transform: translateX(100%);
  }
  100% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
  }
}
.overInitial{
  text-overflow:initial !important;
}
.videoTitle {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  /* display: inline-block; */
  /* white-space: nowrap; */
}

.titleAnimate {
  color: #000;
  display: inline-block;
  white-space: nowrap;
}

.videoImgContentBox {
  position: relative;
}

.playImg {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -25px;
  margin-top: -25px;
  z-index: 99;
}

.playImg img {
  width: 50px;
  height: 50px;
}

.containerleft-line {
  width: 1px;
  height: 100%;
  background: #e9e9e9;
  position: absolute;
  left: 3%;
  z-index: 99;
}

.containerright-line {
  width: 1px;
  height: 100%;
  background: #e9e9e9;
  position: absolute;
  right: 3%;
  z-index: 99;
}

#videoMaking {
  position: relative;
}

@media screen and (max-width: 990px) {
  .videoBox p {
    font-size: 16px !important;
  }

  .photoBoxCoo p {
    margin-top: 24px;
  }

  .topLogoBox {
    position: absolute;
    width: 51px !important;
    top: -5px !important;
    left: 2.5% !important;
    z-index: 110;
  }
}

@media screen and (min-width: 1400px) {
  .topLogoBox {
    position: absolute;
    width: 100px;
    left: 4.15% !important;
    z-index: 110;
  }
}

.topLogoBox {
  position: absolute;
  width: 100px;
  left: 4.5%;
  z-index: 110;
}

.goAli {
  cursor: pointer;
}
</style>



