<template>
  <div id="resource">
    <div class="resource">
      <div class="resource-title">
        <p class="business-titleZ">我们的资源</p>
        <p class="business-titleY">Our Resources</p>
      </div>
      <!--slider-->
      <div class="fuSlide ck-slide imgs rel resource-box">
        <ul class="ck-slide-wrapper">
          <li v-for="(item,index) in ourResourcesList" :key="index">
            <div class="resourceImg">
              <img :src="item.imageUrl">
            </div>
            <div class="banner_con tabTw-tw">
              <div class="gy2 tabTw-info">
                <div class="banner2_one resource-info">
                  <h5 class="anim-1">{{item.resourcesName}}</h5>
                  <p class="anim-2">{{item.resourcesDetails}}</p>
                  <div class="btn-box">
                    <div class="btn-line"></div>
                    <div class="resource-btn" @mouseenter="resourceMouseenter()" @mouseleave="resourceMouseleave()">
                      <div class="resource-btn1"></div>
                      <span @click="detailLink">查看更多</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </li>
          <div class="clearfix"></div>
        </ul>
        <div class="index-waper-box">
          <div class="index-back-box digital">
            <div class="sub-arrows bx-controls-direction">
              <i class="icom prev bx-prev ck-prev"></i>
              <i class="icom next bx-next ck-next"></i>
            </div>
          </div>
        </div>
      </div>
      <!--slider end-->
    </div>
  </div>
</template>

<script>
import "../../../common/js/jquery.SuperSlide.2.1.1.js";
import "../../../common/js/waypoints.min.js";
import "../../../common/js/jquery.ckSlide.js";
import "../../../common/js/index.js";
export default {
  name: "resource",
  props: {
    ourResourcesList: {
      type: Array
    }
  },
  data() {
    return {
      curr: 0
    };
  },
  mounted() {},
  methods: {
    detailLink(){
      this.$router.push({ path:'/industryLeader'})
    },
    resourceMouseenter() {
      $(".resource-btn1").css("transform", "scaleX(1)");
      $(".btn-line").css("transform", "scaleX(0)");
      setTimeout(() => {
        $(".resource-btn")
          .find("span")
          .css("color", "#d19b51");
      }, 600);
    },
    resourceMouseleave() {
      $(".resource-btn1").css("transform", "scaleX(0)");
      $(".btn-line").css("transform", "scaleX(1)");
      setTimeout(() => {
        $(".resource-btn")
          .find("span")
          .css("color", "#fff");
      }, 600);
    }
  }
};
</script>

<style scoped>
@import "../../../common/css/anim.css";
@import "../../../common/css/style.css";
@import "../../../common/css/index.css";
</style>



