<template>
  <div id="businessOne">
    <div class="businessBox">
      <div class="business-title">
        <p class="business-titleZ">我们的业务</p>
        <p class="business-titleY">Our Business</p>
      </div>
      <div class="business-container">
        <div class="businessBottom" v-show="!flag">
          <div class="business-con" v-for="(item, index) in ourBusinessList" :key="index" >
            <div class="row" v-for="(business, idx) in item" :key="idx+'a'" v-show="index%2 == 0">
              <div class="col-md-5">
                <div class="businessTop-left">
                  <img :src="business.imageUrl">
                </div>
              </div>
              <div class="col-md-7">
                <div class="businessTop-right">
                  <div class="business-list">
                    <div class="v-line">
                      <span>0{{index*2+idx+1}}</span>
                    </div>
                    <div class="business-info">
                      <h5 class>{{business.businessTitle}}</h5>
                      <h6 class>{{business.businessSubTitle}}</h6>
                      <p class="business-text">{{business.businessDesc}}</p>
                      <div class="business-btn">
                        <div class="vanish_btn"></div>
                        <div class="div_btn animation_btn">
                          <a @click="goDetail(business.pathCode)" class="btn_el survey-download fix_btn margin_top">
                            <span class="text_link">进入详情</span>
                          </a>
                          <div class="mask_btn"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row" v-for="(business, idx1) in item" :key="idx1+'b'" v-show="index%2 != 0">
              <div class="col-md-7">
                <div class="businessBottom-left pl100">
                  <div class="business-list">
                    <div class="v-line">
                      <span>0{{index*2+idx1+1}}</span>
                    </div>
                    <div class="business-info pr50">
                      <h5 class>{{business.businessTitle}}</h5>
                      <h6 class>{{business.businessSubTitle}}</h6>
                      <p class="business-text">{{business.businessDesc}}</p>
                      <div class="business-btn">
                        <div class="vanish_btn"></div>
                        <div class="div_btn animation_btn">
                          <a @click="goDetail(business.pathCode)" class="btn_el survey-download fix_btn margin_top">
                            <span class="text_link">进入详情</span>
                          </a>
                          <div class="mask_btn"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-md-5">
                <div class="businessBottom-right">
                  <img :src="business.imageUrl">
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="businessTop" v-show="flag">
          <div class="business-con" v-for="(item, index) in ourBusinessList" :key="index">
            <div class="row">
              <div class="col-md-5 businessImgBox">
                <div class="businessTop-left">
                  <img :src="item.imageUrl">
                </div>
              </div>
              <div class="col-md-7">
                <div class="businessTop-right">
                  <div class="business-list">
                    <div class="v-line">
                      <span>0{{index+1}}</span>
                    </div>
                    <div class="business-info">
                      <h5 class>{{item.businessTitle}}</h5>
                      <h6 class>{{item.businessSubTitle}}</h6>
                      <p class="business-text">{{item.businessDesc}}</p>
                      <div class="business-btn">
                        <div class="vanish_btn"></div>
                        <div class="div_btn animation_btn">
                          <a @click="goDetail(item.pathCode)" class="btn_el survey-download fix_btn margin_top">
                            <span class="text_link">进入详情</span>
                          </a>
                          <div class="mask_btn"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "businessOne",
   data() {
    return {
      flag: true
    };
  },
  props:{
      ourBusinessList:{
         type:Array
      }
  },
  mounted() {
    this.leftImg();
  },
  methods: {
    leftImg() {
      if (document.body.offsetWidth <= 800) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
    goDetail(a){
      if(a!='outerLink'){
        this.$router.push({ path:'/'+a})
      }else if(a=='outerLink'){
        window.location.href='https://mv.dashivlog.com/index'
      }
      
    }
  }
};
</script>

<style scoped>
   @import "../../../common/css/index.css"; 
@media screen and (max-width: 990px) {
  .businessImgBox{
     padding: 0 !important;
   }
}
   

</style>



