<template>
  <div id="header2">
    <div id="nav">
      
      <div class="container">
        <div class="row">
          <div class="col-md-12">
            <div class id="header-container-fixed">
              <div class="container">
                <div class>
                  <ul class="nav navbar-nav">
                     <li
                      v-for="(item,index) in headerList"
                      :key="index"
                      v-if="index<=3"
                      tag="li"
                      exact
                    >
                      <router-link :to="item.rouPath" active-class="navActive1">
                        <a class="s-nav-item">
                          <span class="s-font-body1">{{item.headerName}}</span>
                        </a>
                      </router-link>
                    </li>
                  </ul>
                  <div class="navbar-left">
                    <router-link to="/index">
                      <img src="../img/logo2.png">
                    </router-link>
                  </div>
                  <ul class="nav navbar-nav navbar-right">
                    <li
                      v-for="(item,index) in headerList"
                      :key="index"
                      v-if="index>=4"
                      tag="li"
                      exact
                    >
                      <router-link :to="item.rouPath" active-class="navActive1">
                        <a class="s-nav-item">
                          <span class="s-font-body1">{{item.headerName}}</span>
                        </a>
                      </router-link>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../common/js/header.js";

export default {
  data() {
    return {
      isactive: 0,
      menuflag: false,
      screenWidth: document.body.clientWidth,
      headerList: [
        { rouPath: "/cultureCreativity", headerName: "文化创意平台" },
        { rouPath: "/videoMaking", headerName: "影视制片制作" },
        { rouPath: "/marketingPlan", headerName: "营销策划" },
        { rouPath: "/activityExecution", headerName: "活动执行" },
        { rouPath: "/industryLeader", headerName: "行业领军资源" },
        { rouPath: "/productCase", headerName: "产品开发案例" },
        { rouPath: "/cooperation", headerName: "战略合作资源" },
        { rouPath: "/aboutUs", headerName: "关于我们" }
      ]
    };
  },
  mounted() {
    this.menuflag = true;
    this.$router.afterEach((to, from, next) => {
        window.scrollTo(0, 0)
    })
  },
  methods: {
    fn(index) {
      this.isactive = index;
    },
    menu() {
      this.menuflag = !this.menuflag;
    }
  }
};
</script>

<style scoped>
/*header*/
#header-container-fixed > a{
  border-top: 4px solid #fff;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  transition: opacity 3s;
}
.fade-leave-to {
  opacity: 0;
}
.fade-leave-active {
  transition: opacity 3s;
}
.colorOne {
  color: #000 !important;
}
.navbar-default {
  background-color: transparent;
  border-color: transparent;
}
.navbar-toggle {
  float: left;
}
#header-container a {
  color: #fff !important;
}
#header-container-fixed a {
  color: #2f2f2f !important;
  cursor: pointer;
  border-top:solid 4px transparent;
}
#header-container-fixed li {
  cursor: pointer;
}

#header-container li > a:hover {
  background: rgba(236, 236, 236, 0.2);
}
.navbar-small a {
  color: #666 !important;
}
a {
  text-decoration: none;
}
.navbar-default .navbar-toggle:focus,
.navbar-default .navbar-toggle:hover {
  background-color: #cba365;
}

.navbar-default .navbar-toggle {
  background-color: #cba365;
}

.navbar-default .navbar-toggle {
  border-color: #cba365;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: #fff;
}

.navbar-nav {
  display: inline-block;
  zoom: 1;
  padding: 0px 0px 5px 0;
  vertical-align: middle;
}

/*.navbar-nav>li {
    padding-top: 10px;
}*/

.s-font-body {
  color: #fff;
  font-size: 16px;
}

.navbar-left img {
  width: 90px;
}

.nav > li > a {
  padding: 40px 22px 20px 22px;
}

.navbar-left {
  padding-left: 12px;
}

.navActive1 {
  background: rgba(236, 236, 236, 1);
  box-sizing: border-box;
  border-top: 4px solid #c89f62 !important;
}
#header-container-fixed li > a:hover {
  background: rgba(236, 236, 236, 1);
  box-sizing: border-box;
  border-top: 4px solid #c89f62 !important;
}
.navActive1 .s-nav-item {
  padding-top: 36px;
}

.navActive {
  background: rgba(236, 236, 236, 0.2);
}

#header-container {
  position: absolute;
  z-index: 9999;
  top: 0;
  width: 100%;
  text-align: center;
}

#header-container-fixed {
  background: #fff;
}

#header-container-fixed {
  background: white;
  position: fixed;
  z-index: 210;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 16px 0 rgba(0,0,0,0.1);
  width: 100%;
}

/* #header-container-fixed.animated {
  opacity: 1;
  top: -1px;
  -webkit-transform: none;
  transform: none;
  pointer-events: all;
} */

.s-font-body1 {
  color: #2f2f2f;
  font-size: 16px;
}
.container {
  padding: 0 !important;
}
#header-container {
  padding: 0 0 25px 0;
  -webkit-animation-name: navDown;
  animation-name: navDown;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s;
  -webkit-animation-timing-function: ease-out;
  animation-timing-function: ease-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  z-index: 9998 !important;
}
#header-container .nav-center {
  min-width: 150px;
}
#header-container .nav-item,
#header-container-fixed .nav-item {
  display: inline-block;
  zoom: 1;
  padding: 10px 22px;
  vertical-align: middle;
}

.bgMask {
  background: #000;
  opacity: 0.7;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  z-index: 101;
}

@media screen and (max-width: 1570px) {
}

@media screen and (max-width: 1400px) {
}

@media screen and (max-width: 1200px) {
  .s-font-body,
  .s-font-body1 {
    font-size: 14px;
  }
  .nav > li > a {
    padding: 15px 17px 8px 17px;
  }
  .navActive1 .s-nav-item {
    padding-top: 11px;
  }
  .navbar-left img {
    width: 50px;
  }
}

@media screen and (max-width: 992px) {
  .s-font-body {
    font-size: 14px;
  }
  .navActive1 .s-nav-item {
    padding-top: 6px;
  }
  .nav > li > a {
    padding: 10px 6px;
  }
  .navbar-left img {
    width: 40px;
  }
  .navbar-left {
    padding-left: 5px;
  }
  .menuImg {
    width: 50px;
    position: fixed;
    top: 20px;
    left: 11px;
    display: block;
    z-index: 9999;
  }
  .leftTab {
    position: fixed;
    top: 50%;
    margin-top: -210px;
    left: 50%;
    margin-left: -130px;
    z-index: 102;
    font-size: 16px;
    line-height: 12px;
    display: none;
  }
  .closeImg {
    width: 50px;
    position: absolute;
    top: -25px;
    right: -25px;
    display: block;
  }
  .leftTab ul {
    background: #fff;
    color: #979797;
    padding: 20px 70px;
  }
  .leftTab li {
    padding: 16px 0px;
  }
}
</style>


