<template>
  <div id="videoCase">
    <div id="wrap">
      <!-- 内容区域 -->
      <!--线-->
      <div class="containerleft-line"></div>
      <div class="containerright-line"></div>
      <div class="videoBanner">
        <div class="bannerBottom-line"></div>
        <img src="../../img/yingshizhizuo_bannervedio_top001.png">
      </div>
      <div class="videoContainer">
        <div class="video-navBox">
          <div class="container">
            <div class="tag">
              <router-link to="/index">
                <span class="tagIndex">首页</span>
              </router-link>&nbsp;>&nbsp;全部视频
            </div>
          </div>
          <div class="video-nav">
            <div class="video-nav-h">
              <div class="container">
                <ul>
                  <li v-for="(item,index) in movieTypeList" :key="index" :class="{selected:index==isactive}"
                    @click="fn(index,item.typeId,item.typeName)">{{item.typeName}}</li>
                </ul>
              </div>
            </div>
            <!-- <div class="video-nav-h-details">
              <div class="container">
                <ul>
                  <li>全部</li>
                  <li>金牌丈母娘</li>
                  <li>健身型不型</li>
                  <li>乐嗨乐开心</li>
                  <li>嗨长安</li>
                  <li>植男测评</li>
                  <li>颜值装修队</li>
                </ul>
              </div>
            </div>-->
          </div>
        </div>
        <div class="videoBox">
          <div class="container" :key="containKey">
            <div class="row">
              <div class="col-md-6 col-lg-3" v-for="(item,index) in list " :key="index">
                <div class="videoInfo">
                  <div class="videoImg" @click="msgShow(item.caseId,item.movieUrl)">
                    <img :src="item.coverImgUrl">
                    <div class="playImg">
                      <img src="../../img/play_001.png">
                    </div>
                    <div class="video-time">{{item.duration}}</div>
                  </div>
                  <div class="plr20">
                    <div @mouseenter="titleRole(item.caseId)" :id="'title'+item.caseId" class="video-title"
                      titleFlag="true">
                      <p class="titleAnimate">{{item.title}}</p>
                    </div>
                    <div class="video-title-tag">
                      <ul class="tagbq">
                        <li v-for="(item1,index) in item.labels" :key="index" v-if="index<3">#{{item1.labelName}}</li>
                      </ul>
                    </div>
                    <div class="video-text">
                      <h6>简介</h6>
                      <p>{{item.description}}</p>
                    </div>
                  </div>
                  <div class="video-position" v-show="activeIndex===item.caseId">
                    <div class="video-play">
                      <div class="video-video">
                        <video :id="item.caseId" controls="controls" :poster="item.coverImgUrl">
                          <!-- <source :src="item.movieUrl" type="video/mp4"> -->
                        </video>
                      </div>
                      <div class="video-close">
                        <img src="../../img/close_001.png" @click="msgClose(item.caseId)">
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="clearfix"></div>
              <div id="page">
                <el-pagination background layout="prev, pager, next" :page-size="extraInfo.pageSize"
                  :current-page.sync="pageNum" :total="this.pageA" @current-change="currentPageChange"
                  :hide-on-single-page="true"></el-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--视频弹窗-->

      <!-- 底部-->
      <footer-three></footer-three>
    </div>
  </div>
</template>

<script>
  import {
    initShare
  } from "@/utils/wechatShare.js";
  import {
    Loading
  } from "element-ui";
  import footerThree from "../../components/footer3";

  export default {
    data() {
      return {
        movieCaseByPageList: [],
        pageNum: 1,
        movieTypeList: [],
        isactive: 0,
        extraInfo: [],
        activeIndex: "",
        classifyName: "",
        pageA: 0,
        typeId: "",
        containKey: 1
      };
    },
    created() {
      this.pageNum = Number(sessionStorage.getItem("pageNum")) || 1;
    },
    mounted() {
      this.movieCaseByPage();
      this.movieType();
      const shareData = {
        title: "视频营销一站式解决方案",
        desc: "中旌影视传媒",
        link: location.href,
        imgUrl: "https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/videoCase111.png",
        signUrl: encodeURIComponent(location.href)
      };
      initShare(shareData);
    },
    methods: {
      movieCaseByPage() {
        var self = this;
        var params = new URLSearchParams();
        params.append("pageNum", this.pageNum);
        params.append("typeId", this.typeId);
        return self
          .$request({
            url: "film/making/getMovieCaseByPage",
            method: "get",
            params: params
          })
          .then(res => {
            // console.log(res);
            this.movieCaseByPageList = res.results;
            this.extraInfo = res.extraInfo;
            this.pageA = res.extraInfo.totalRecords;
          })
          .catch(error => {
            console.log(error);
          });
      },
      currentPageChange(pageNum) {
        // $('.video-title').css('animation-play-state', 'paused');
        Loading.service();
        this.pageNum = pageNum;
        sessionStorage.setItem("pageNum", pageNum);
        this.containKey++
        this.movieCaseByPage().finally(() => {
          Loading.service().close();
        });
      },
      movieType() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "film/making/getMovieTypeList",
            method: "get",
            params: params
          })
          .then(res => {
            this.movieTypeList = res.results;
            // console.log(res);
          })
          .catch(error => {
            console.log(error);
          });
      },
      fn(index, typeId, typeName) {
        //点击切换 变量的值 赋值为 index
        this.isactive = index;
        if (typeName == "全部") {
          this.typeId = "";
        } else {
          this.typeId = typeId;
        }

        this.pageA = this.list.length;
        this.pageNum = 1;
        // console.log(this.pageNum)
        this.movieCaseByPage();
      },
      //打开视频
      msgShow(e, x) {
        this.activeIndex = e;
        this._dom = document.getElementById(e);
        this._dom.setAttribute("src", x);
        this._dom.play();
      },
      //关闭视频
      msgClose(e) {
        this.activeIndex = !e;
        this._dom = document.getElementById(e);
        this._dom.pause();
        this._dom.setAttribute("src", "");
      },
      titleRole(e) {
        let titleDom = document.getElementById("title" + e);
        let titelWidth = titleDom.firstChild.offsetWidth;
        let parentWidth = titleDom.offsetWidth;
        if (titelWidth >= parentWidth && titleDom.getAttribute("titleFlag")) {
          titleDom.setAttribute("titleFlag", false);
          titleDom.firstChild.classList.add("titleMove");
          titleDom.classList.add("overInitial");
          setTimeout(() => {
            titleDom.firstChild.classList.remove("titleMove");
            titleDom.classList.remove("overInitial");
            titleDom.setAttribute("titleFlag", true);
          }, 8000);
        }
      }
    },
    components: {
      footerThree
    },
    computed: {
      list() {
        return this.typeId ?
          this.movieCaseByPageList.filter(i => i.typeId === this.typeId) :
          this.movieCaseByPageList;
      }
    }
  };

</script>

<style scoped>
  @import "../../common/css/videoCase.css";

  .titleMove {
    animation: 8s wordsLoop linear infinite normal;
  }

  @keyframes wordsLoop {
    0% {
      transform: translateX(0px);
      -webkit-transform: translateX(0px);
    }

    50% {
      transform: translateX(-100%);
      -webkit-transform: translateX(-100%);
    }

    50.01% {
      transform: translateX(100%);
      -webkit-transform: translateX(100%);
    }

    100% {
      transform: translateX(0px);
      -webkit-transform: translateX(0px);
    }
  }

  @-webkit-keyframes wordsLoop {
    0% {
      transform: translateX(0px);
      -webkit-transform: translateX(0px);
    }

    50% {
      transform: translateX(-100%);
      -webkit-transform: translateX(-100%);
    }

    50.01% {
      transform: translateX(100%);
      -webkit-transform: translateX(100%);
    }

    100% {
      transform: translateX(0px);
      -webkit-transform: translateX(0px);
    }
  }

  .videoTitle {
    width: 99%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .titleAnimate {
    color: #000;
    display: inline-block;
    white-space: nowrap;
  }

  .overInitial {
    text-overflow: initial !important;
  }

  @media screen and (max-width: 992px) {
    .videoBanner img {
      width: 200% !important;
      margin-left: -50%;
    }
  }

  #page {
    padding: 70px 0 100px 0;
  }

  .el-pagination {
    text-align: center;
  }

  .el-pager /deep/ li.active {
    color: #fc1c28 !important;
    font-size: 14px;
  }

  .el-pagination.is-background /deep/ .el-pager li:not(.disabled).active {
    background-color: #c0812a !important;
    color: #fff;
  }

  .el-pager /deep/ li {
    padding: 0 16px;
    font-size: 13px;
    min-width: 35.5px;
    height: 34px;
    line-height: 34px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    text-align: center;
  }

  .el-pagination.is-background .btn-next,
  .el-pagination.is-background .btn-prev,
  .el-pagination.is-background /deep/ .el-pager li:hover {
    color: #fc1c28;
  }

  .el-pagination.is-background .btn-next,
  .el-pagination.is-background .btn-prev,
  .el-pagination.is-background /deep/ .el-pager li {
    margin: 0 5px;
    background-color: #fff;
    color: #303030;
    min-width: 38px;
    border-radius: 2px;
  }

  .tagbq {
    height: 30px;
    overflow: hidden;
    /*超出部分隐藏*/
    white-space: nowrap;
    /*不换行*/
    text-overflow: ellipsis;
  }

  .selected {
    background: #c0812a !important;
    color: #fff !important;
  }

</style>



