<template>
  <div id="industryLeader">
    <!-- 头部 -->
    <header-mobile v-show="!isPc"></header-mobile>
    <header-two v-show="isPc"></header-two>
    <!--中间内容区域-->
    <div class="main">
      <div class="bannerImg">
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(item,index) in bannerList" :key="index">
            <img :src="item.imageUrl">
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="infoBox">

        <div class="leftInfo row" v-for="(item,index) in resourceList" :key="'info1'+index" v-show="index%2==0">
          <div class="leftImg col-md-6">
            <img :src="item.imageUrl">
          </div>
          <div class="rightText col-md-6">
            <div class="topLogo">
              <img :src="item.icoUrl">
              <div class="rightTitle">
                <div class="titleNum fl-r">{{item.sort| changeNum()}}</div>
                <div class="clearfix"></div>{{item.resourcesTitle}}
              </div>
            </div>
            <div class="textContent" v-html="item.resourcesDesc"></div>
          </div>
        </div>

        <div class="rightInfo row" v-for="(item,index) in resourceList" :key="'info2'+index" v-show="index%2!=0">
          <div class="rightText col-md-6">
            <div class="topLogo">
              <img :src="item.icoUrl">
              <div class="leftTitle">
                <div class="titleNum fl-l">{{item.sort| changeNum()}}</div>
                <div class="clearfix"></div>{{item.resourcesTitle}}
              </div>
              <div class="clearfix"></div>
            </div>
            <div class="textContent" v-html="item.resourcesDesc"></div>
          </div>
          <div class="leftImg col-md-6">
            <img :src="item.imageUrl">
          </div>
          <div class="clearfix"></div>

        </div>

        <!-- 图片展示 -->
        <div class="photoList" v-for="(item,index) in showImg" :key="index">
          <div class="topTitle">
            <img src="../../img/icon_elememt@2x.png">
            <p class="titleText">{{item.description}}</p>
            <p class="titleEn">Picture Show</p>
          </div>
          <div class="photoBox row">
            <div class="col-md-6 col-lg-3 imgBox" v-for="(item, index) in (item.details)" :key="index">
              <img :src="item.imageUrl">
            </div>
          </div>
        </div>

        <div class="footerImg">
          <img src="../../img/hangyelingjunziyuan_footer_pic@2x.jpg">
        </div>
      </div>
    </div>
    <!-- 底部-->
    <footer-two></footer-two>
  </div>
</template>

<script>
  import {
    initShare
  } from '@/utils/wechatShare.js'
  import headerTwo from "../../components/header2";
  import headerMobile from "../../components/headerMobile";
  import footerTwo from "../../components/footer2";
  import { Loading } from 'element-ui';

  import "../../common/js/industryLeader.js";
  export default {
    data() {
      return {
        bannerList: [],
        showImg: [],
        resourceList: [],
        clientType: '',
        isPc: true
      };
    },
    components: {
      headerTwo,
      footerTwo,
      headerMobile
    },
    mounted() {
      Loading.service();
      this.setWidth();
        this.getBanner().finally(() => {
          this.getShowImg().finally(() => {
            this.getResource();
            Loading.service().close();
          });
        });
     




      const shareData = {
        title: '影视传媒行业中的领军企业',
        desc: '中旌影视传媒',
        link: location.href,
        imgUrl: 'https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/leader111.png',
        signUrl: encodeURIComponent(location.href)
      }
      initShare(shareData)

    },
    methods: {
      setWidth() {
        var widNum = $(window).width();
        if (widNum > 990) {
          this.clientType = '';
          this.isPc = true
        } else {
          this.clientType = 'MOBILE';
          this.isPc = false
        }
      },
      getBanner() {
        var self = this;
        var params = new URLSearchParams();
        params.append("clientType", this.clientType);
        return self
          .$request({
            url: "industry/resources/getBannerList",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response)
            this.bannerList = response.results;
          })
          .catch(error => {
            console.log(error);
          });
      },
      getShowImg() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "industry/resources/getShowImageList",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response)
            this.showImg = response.results;
          })
          .catch(error => {
            console.log(error);
          });
      },
      getResource() {
        var self = this;
        var params = new URLSearchParams();
        return self
          .$request({
            url: "industry/resources/getResourcesList",
            method: "get",
            params: params
          })
          .then(response => {
            // console.log(response);
            this.resourceList = response.results;
          })
          .catch(error => {
            console.log(error);
          });
      }
    }
  };

</script>

<style scoped>
  @import "../../common/css/industryLeader.css";

  .bannerImg img {
    width: 100%;
  }

  @media screen and (max-width: 992px) {
    .titleText {
      font-size: 24px;
    }

    .titleEn {
      font-size: 18px;
    }
  }

</style>



