<template>
  <div id="cultureCreativity">
    <div id="wrap">
      <!-- 头部 -->
      <header-mobile v-show="!isPc"></header-mobile>
      <header-two v-show="isPc"></header-two>
      <!-- 内容区域 -->
      <div class="main">
        <div class="margin_top">
          <img src="../../img/wenhuachuangyipingtai_banner01.jpg">
        </div>
        <div class="main-container">
          <div class="container animateList">
            <h6 class="cultureTitle">年轻人的事都是“大视”</h6>
            <div class="ultureText">
              中旌影视秉承“互联网+”的运营理念，联合品牌推广，IP内容孵化，打造年轻人文化创意服务平台。通过聚焦大学生及年轻人所喜爱且关注的文化与影视领域，整合业内专家、专业机构、意见领袖、民间高手及有影视梦想的大学生及年轻人，打造IP内容孵化，以发现优秀作品，发掘优质人才，发布新锐创意为己任，着力构建全新模式的文化创意及影视项目孵化产业链。
            </div>
            <div class="cultureImg">
              <img src="../../img/wenhuachuangyipingtai_pic001.png">
            </div>
          </div>
          <div class="cultureInfo animateList">
            <div class="container">
              <div class="icon_elememt">
                <img src="../../img/icon_elememt.png">
              </div>
              <h6 class="cultureTitle">「 大视 」APP</h6>
              <div class="ultureText">
                国内泛大学生群体的潮流聚集区，一个视频，一个资讯，一项搞怪的创意，放飞个性张扬自我，全方位展示自在生活。海量多元潮流短视频，轻松搞笑，体现校园生活，展示自我风采：校园迎新、营销大赛、艺术展览、各色讲座……用最酷最嗨的花样活动，激发你的创作热情；流行潮酷资讯汇集，影剧、明星、文创、八卦、美妆、穿搭、时事、生活……有态度有生活，满足受众所有好奇心。
              </div>
              <div class="cultureImg">
                <img src="../../img/wenhuachuangyipingtai_pic002.jpg">
              </div>
            </div>
            <div class="web_bg">
              <div class="mobileImg">
                <img src="../../img/wenhuachuangyi_pic_mobile001.png">
              </div>
            </div>
          </div>
          <div class="cultureInfo animateList">
            <div class="container">
              <div class="icon_elememt">
                <img src="../../img/icon_elememt.png">
              </div>
              <h6 class="cultureTitle">「 大视VLOG 」APP</h6>
              <div class="ultureText">
                大视vlog 力求打造全国首个最纯净的VLOG平台，既是简单高效的视频创作工具：拍摄-剪辑-拼接排序-编辑视频-发布，流程简单、体验流畅，又是一个贴心的视频编辑小工具：电影同款滤镜、贴合热点的拍摄模板，各种贴纸、字幕、变速，音乐等，逼格超高。 
              </div>
              <div class="cultureImg">
                <img src="../../img/wenhuachuangyipingtai_pic003.png">
              </div>
            </div>
            <div class="web_bg">
              <div class="mobileImg">
                <img src="../../img/wenhuachuangyi_pic_mobile002.png">
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 底部 -->
      <footer-two></footer-two>
    </div>
  </div>
</template>

<script>
  import { initShare } from '@/utils/wechatShare.js'
  import "../../common/js/cultureCreativity.js";
  import headerTwo from "../../components/header2";
  import headerMobile from "../../components/headerMobile";
  import footerTwo from "../../components/footer2";

  export default {
    data() {
      return {
        isPc: true
      };
    },
    mounted() {
      this.setWidth()

const shareData = {
      title: '因为专注，所以专业',
      desc: '中旌影视传媒',
      link: location.href,
      imgUrl:'https://zhongjingwebsite.oss-cn-beijing.aliyuncs.com/gw/share/cultrul111.png.png',
      signUrl: encodeURIComponent(location.href)
    }
    initShare(shareData)
    },
    methods: {
      setWidth() {
        var widNum = $(window).width();
        if (widNum > 990) {
          this.clientType = '';
          this.isPc = true
        } else {
          this.clientType = 'MOBILE';
          this.isPc = false
        }
      }
    },
    components: {
      headerTwo,
      footerTwo,
      headerMobile
    }
  };

</script>

<style scoped>
  @import "../../common/css/cultureCreativity.css";

  @media screen and (max-width: 992px) {
    .margin_top img {
      width: 200% !important;
      margin-left: -50%;
    }
    .margin_top{
      overflow: hidden;
    }
  }

</style>



